<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" :action="'/oa/items/' + q.itemId + '/item/skus'" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['price'] }}</div>
            <input type="number" v-model="price" class="border" v-placeholder min="0" step="0.01" v-autofocus required>
            <input type="hidden" name="price" :value="Math.round(price * 100)">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['qty'] }}</div>
            <input type="number" name="qty" class="border" v-placeholder required min="0">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['etd'] }}</div>
            <input type="number" name="etd" class="border" v-placeholder min="0">
          </label>
          <div></div>
          <template v-for="i in kvsCnt">
            <label class="col-md-9">
              <div>{{ q.data.i18n['prop']['model'] }}: {{ q.data.i18n['prop']['key'] }} {{ q.data.i18n['prop']['value'] }} (空格分开)</div>
              <input type="text" name="bulk[]" class="border" v-placeholder required>
            </label>
            <div class="col-md-3" v-if="i === kvsCnt">
              <div>
                <a href="#" class="btn py-em" @click.prevent="kvsCnt += 1" v-if="i === kvsCnt">添加</a>
                <a href="#" class="btn py-em c-danger" @click.prevent="confirmDestroy($event) && (kvsCnt -= 1)" v-if="i === kvsCnt && kvsCnt > 1">删除</a>
              </div>
            </div>
            <div></div>
          </template>
          <div></div>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      price: 0,
      kvsCnt: 1
    }
  },
  methods: {
    go(response) {
      response.data['skus'].forEach((sku) => {
        this.q.data.skus.push(sku)
      })
      response.data['props'].forEach((prop) => {
        this.q.data.props.push(prop)
      })
      this.$emit('close')
    }
  }
}
</script>
