import Vue from 'vue'
import Router from 'vue-router'
import Pages from '../pages/**/*.vue'

import Session from '/concerns/session'
import Wechat from '/concerns/wechat'

Vue.use(Router)

const get = function (path, opts = {}) {
  opts.path || (opts.path = path)
  opts.name || (opts.name = path.substr(1).replace(/\//g, '-'))
  opts.component || (opts.component = opts.name.split('-').reduce((pages, key) => pages[key], Pages).default)
  opts.component.name = opts.name
  return opts
}

const router = new Router({
  mode: 'history',
  routes: [].concat(
    get('/signin', { meta: { authless: true } }),
    get('/terms_of_use', { meta: { authless: true } }),
    get('/privacy_policy', { meta: { authless: true } }),
    get('/home', { alias: '/', meta: { authless: true } }),
  ).concat(
    get('/wechat/oap/auth', { meta: { authless: true } })
  ).concat(
    get('/oa/home', { alias: '/oa', meta: { permanent: true } }),
    get('/oa/users', { name: 'oa-users-index', meta: { permaback: true } }),
    get('/oa/users/new', { meta: { permabackable: true } }),
    get('/oa/users/:id', { name: 'oa-users-show', meta: { permabackable: true } }),
    get('/oa/users/:id/edit', { name: 'oa-users-edit', meta: { permabackable: true } }),
    get('/oa/op/banners', { name: 'oa-op-banners-index', meta: { permaback: true } }),
    get('/oa/op/banners/new', { meta: { permabackable: true } }),
    get('/oa/op/banners/:id', { name: 'oa-op-banners-show', meta: { permabackable: true } }),
    get('/oa/op/banners/:id/edit', { name: 'oa-op-banners-edit', meta: { permabackable: true } }),
    get('/oa/op/topics', { name: 'oa-op-topics-index', meta: { permaback: true } }),
    get('/oa/op/topics/new', { meta: { permabackable: true } }),
    get('/oa/op/topics/:id', { name: 'oa-op-topics-show', meta: { permabackable: true } }),
    get('/oa/op/topics/:id/edit', { name: 'oa-op-topics-edit', meta: { permabackable: true } }),
    get('/oa/op/subtopics/new', { meta: { permabackable: true } }),
    get('/oa/op/subtopics/:id/edit', { name: 'oa-op-subtopics-edit', meta: { permabackable: true } }),
    get('/oa/brands', { name: 'oa-brands-index', meta: { permaback: true } }),
    get('/oa/brands/new', { meta: { permabackable: true } }),
    get('/oa/brands/:id', { name: 'oa-brands-show', meta: { permabackable: true } }),
    get('/oa/brands/:id/edit', { name: 'oa-brands-edit', meta: { permabackable: true } }),
    get('/oa/shops', { name: 'oa-shops-index', meta: { permaback: true } }),
    get('/oa/shops/new', { meta: { permabackable: true } }),
    get('/oa/shops/:id', { name: 'oa-shops-show', meta: { permabackable: true } }),
    get('/oa/shops/:id/edit', { name: 'oa-shops-edit', meta: { permabackable: true } }),
    get('/oa/item/categories', { name: 'oa-item-categories-index', meta: { permaback: true } }),
    get('/oa/item/categories/:id', { name: 'oa-item-categories-show', meta: { permabackable: true } }),
    get('/oa/items', { name: 'oa-items-index', meta: { permaback: true } }),
    get('/oa/items/:id', { name: 'oa-items-show', meta: { permabackable: true } }),
    get('/oa/item/selects', { name: 'oa-item-selects-index' }),
    get('/oa/item/tags', { name: 'oa-item-tags-index' }),
    get('/oa/items/:itemId/skus', { name: 'oa-items-skus-index' })
  ).concat(
    get('/mbr/main/index', { alias: '/mbr', meta: { authless: true, permanent: true } }),
    get('/mbr/main/me', { alias: '/mbr/me' }),
    get('/mbr/main/settings', { alias: '/mbr/settings' }),
    get('/mbr/user', { name: 'mbr-user-show' }),
    get('/mbr/user/edit'),
    get('/mbr/user/password/edit'),
    get('/mbr/user/addresses', { name: 'mbr-user-addresses-index' }),
    get('/mbr/favorites', { name: 'mbr-favorites-index' }),
    get('/mbr/op/topics/:id', { name: 'mbr-op-topics-show', meta: { authless: true } }),
    get('/mbr/item/categories', { name: 'mbr-item-categories-index', meta: { authless: true, permanent: true } }),
    get('/mbr/items/missing', { meta: { authless: true } }),
    get('/mbr/items', { name: 'mbr-items-index', meta: { authless: true, permaback: true } }),
    get('/mbr/items/:id', { name: 'mbr-items-show', meta: { authless: true, permabackable: true } }),
    get('/mbr/item/cart', { name: 'mbr-item-cart-show' }),
    get('/mbr/item/orders', { name: 'mbr-item-orders-index' }),
    get('/mbr/item/orders/new'),
    get('/mbr/item/orders/:id', { name: 'mbr-item-orders-show' })
  ).concat(
    get('*', { name: 'not_found', meta: { authless: true } })
  ),
  scrollBehavior(to, from, savedPosition) {
    // Restore the scroll position if set.
    return to.meta.scrollPosition || { x: 0, y: 0 }
  }
})
router.beforeEach((to, from, next) => {
  if (Wechat.oap.openidFetchable()) {
    next({
      path: '/wechat/oap/auth',
      query: { redirection: to.fullPath },
      replace: true
    })
    return
  }

  // Remember the scroll position for permanent pages.
  from.meta.scrollPosition = from.meta.permanent ? { x: window.scrollX, y: window.scrollY } : null

  // Dynamically set permanent from permabackable pages.
  if (to.meta.permaback) {
    to.meta.permanent = to.meta.hasOwnProperty('permanent') ? !!from.meta.permabackable : true
  }

  let externalUrl = to.fullPath.match(/^\/?(https?:\/\/.+)$/)
  if (!externalUrl) {
    if (to.matched.some(record => !record.meta.authless)) { // Require auth.
      if (!Session.signedIn()) {
        next({
          path: '/signin',
          query: { redirection: to.fullPath },
          replace: true
        })
      } else {
        next()
      }
    } else {
      next()
    }
  } else {
    externalUrl = externalUrl[1]
    if (externalUrl.startsWith(location.origin)) { // It is internal.
      next({
        path: externalUrl.substr(location.origin.length)
      })
    } else { // It is really an external url.
      next(false)
      window.open(externalUrl) // location.href = externalUrl
    }
  }
})
// router.afterEach((to, from) => {
// })

export default router
