<template>
  <r-div class="page" data-url="/oa/op/banners" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th>{{ i18n.id }}</th>
              <th>{{ i18n.image }}</th>
              <th>{{ i18n.title }}</th>
              <th>{{ i18n.valid_from }}</th>
              <th>{{ i18n.valid_to }}</th>
              <th>{{ i18n.dri }}</th>
              <th>{{ i18n.url }}</th>
              <th><router-link to="/oa/op/banners/new">新建</router-link></th>
            </tr>
            <tr v-for="(banner, i) in banners">
              <td><router-link :to="{ name: 'oa-op-banners-show', params: { id: banner.id } }">{{ banner.id }}</router-link></td>
              <td><img :data-src="banner.image" width="100" height="75"></td>
              <td>{{ banner.title }}</td>
              <td v-datetime="banner.valid_from"></td>
              <td v-datetime="banner.valid_to"></td>
              <td>{{ banner.dri }}</td>
              <td>{{ banner.url }}</td>
              <td>
                <router-link :to="{ name: 'oa-op-banners-edit', params: { id: banner.id } }">编辑</router-link>
                <r-a :href="'/oa/op/banners/' + banner.id" data-method="delete" @ok="banners.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      banners: {}
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.banners = response.data.banners
    }
  }
}
</script>
