<template>
  <form @submit.prevent="submit" :data-loading="loading"><slot></slot></form>
</template>

<script>
import axios from '/concerns/axios'

export default {
  data() {
    return {
      loading: false
    }
  },
  methods: {
    submit() {
      if (this.loading) {
        return
      }
      this.loading = this.$el.dataset.loadingName || 'fullscreen'
      const btn = this.$el.querySelector('[type="submit"]')
      btn && (btn.disabled = true)
      const method = this.$el.getAttribute('method') || 'post'
      axios({
        method: method,
        url: this.$el.getAttribute('action'),
        data: method !== 'get' ? new FormData(this.$el) : null,
        params: method === 'get' ? new URLSearchParams(new FormData(this.$el)) : null
      }).then((response) => {
        this.$emit('ok', response)
      }).catch((error) => {
        this.handleError(error)
        this.$emit('fail', error)
      }).then(() => {
        btn && (btn.disabled = false)
        this.loading = false
      })
    }
  }
}
</script>
