<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" :action="'/oa/items/' + q.data.item['id'] + '/item/sections'" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['section']['doi'] }}</div>
            <input type="number" name="doi" class="border" v-placeholder min="1">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['section']['image'] }}</div>
            <input type="file" name="image" class="border" v-placeholder>
          </label>
          <label class="col-12">
            <div>{{ q.data.i18n['section']['content'] }}</div>
            <input type="text" name="content" class="border" v-placeholder v-autofocus>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  methods: {
    go(response) {
      this.q.data.sections.push(response.data)
      this.$emit('close')
    }
  }
}
</script>
