<template lang="html">
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">{{ res.i18n['address_'] }}</div>
      <button
        ref="newToggler"
        type="button"
        class="r"
        @click="toggle.new = true">
        添加
      </button>
    </header>
    <main class="ctn">
      <div class="list w-seps w-seps-tb">
        <div class="li" v-for="address in res.addresses">
          <main class="my-em">
            <div class="bolder">{{ address.name }} {{ address.mobile }}</div>
            <div class="fs-sm mt-md">
              <span class="badge c-secondary address-defaulted" v-if="address['defaulted']">默认</span>
              <span class="c-info">{{ `${address.province} ${address.city} ${address.district} ${address.street}` }}</span>
            </div>
          </main>
          <button
            type="button"
            class="fs0 py-em"
            @click="toggle.edit = true; toggle.toggler.edit = $event.currentTarget; editAddressId = address['id']">
            <div class="pl-em w-sepv">编辑</div>
          </button>
        </div>
      </div>

      <address-new
        :active="toggle.new"
        :toggler="$refs.newToggler"
        @close="toggle.new = false"
        @ok="requestAddresses()">
      </address-new>

      <address-edit
        :active="toggle.edit"
        :toggler="toggle.toggler.edit"
        :address-id="editAddressId"
        @close="toggle.edit = false"
        @ok="requestAddresses()">
      </address-edit>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        i18n: {},
        addresses: []
      },
      toggle: {
        new: false,
        edit: false,
        toggler: {
          edit: null
        }
      },
      editAddressId: null
    }
  },
  mounted() {
    this.requestAddresses()
  },
  methods: {
    requestAddresses() {
      this.ajax('/mbr/user/addresses')
    }
  }
}
</script>

<style lang="css" scoped>
.address-defaulted {
  background-color: rgba(193, 160, 99, .1);
}
</style>
