<template>
  <r-div class="page" data-url="/oa/users" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th v-for="key in keys">{{ key }}</th>
              <th><router-link to="/oa/users/new">新建</router-link></th>
            </tr>
            <tr v-for="attrs in list">
              <td><router-link :to="{ name: 'oa-users-show', params: { id: attrs[0] } }">{{ attrs[0] }}</router-link></td>
              <td>{{ attrs[1] }}</td>
              <td>{{ attrs[2] }}</td>
              <td>{{ attrs[3] }}</td>
              <td>{{ attrs[4] }}</td>
              <td>{{ attrs[5] }}</td>
              <td><router-link :to="{ name: 'oa-users-edit', params: { id: attrs[0] } }">编辑</router-link></td>
            </tr>
          </table>
        </div>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      keys: [],
      list: []
    }
  },
  methods: {
    come(response) {
      response.data[0].forEach((item, i) => {
        this.keys.push(item)
      })
      response.data[1].forEach((item, i) => {
        this.list.push(item)
      })
    }
  }
}
</script>
