<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" action="/oa/item/taggings" @ok="go">
          <input type="hidden" name="item_id" :value="q.data.item['id']">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['tag'] }}</div>
            <select name="tag_id" value="" class="border" required v-autofocus>
              <option value="">请选择</option>
              <option v-for="tag in selectableTags()" :value="tag['id']">{{ tag['name'] }}</option>
            </select>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  computed: {
    currentTagIds() {
      return this.q.data.item['taggings'].map((tagging) => tagging['tag_id'])
    }
  },
  methods: {
    go(response) {
      this.q.data.item.taggings.push(response.data)
      this.$emit('close')
    },
    selectableTags() {
      return this.q.data.tags.filter((tag) => !this.currentTagIds.includes(tag['id']))
    }
  }
}
</script>
