<template lang="html">
  <div class="page bg">
    <header class="ctn">
      <back-link v-if="$route.query['backable']"></back-link>
      <div class="c">购物车</div>
      <button type="button" class="r" @click.prevent="destroying = !destroying" v-if="pageNotEmpty">{{ destroying ? '完成' : '编辑' }}</button>
    </header>
    <footer class="ctn fs-sm">
      <mbr-main-tabs current="cart" v-if="!$route.query['backable']"></mbr-main-tabs>
      <template v-if="pageNotEmpty">
        <div class="li bg w-sep" :class="$route.query['backable'] || 'subfooter'" v-if="destroying">
          <main class="pdl">
            <label>
              <input class="reset" type="checkbox" :checked="destroyCheckedSkuships.length > 0 && destroyCheckedSkuships.length === checkableSkuships.length" @change="checkableSkuships.forEach((skuship) => skuship.destroyChecked = $event.target.checked)">
              {{ destroyCheckedSkuships.length > 0 ? `已选 ${destroyCheckedSkuships.reduce((n, skuship) => n + skuship['qty'], 0)}` : '全选' }}
            </label>
          </main>
          <button type="button" class="w30 btn primary" :disabled="destroyCheckedSkuships.length < 1" @click.prevent="destroySkuships(destroyCheckedSkuships, $event, () => { destroying = false })">
            删除
          </button>
        </div>
        <div class="li bg w-sep" :class="$route.query['backable'] || 'subfooter'" v-else>
          <main class="pdl fs0">
            <label>
              <input class="reset" type="checkbox" :checked="checkedSkuships.length > 0 && checkedSkuships.length === checkableSkuships.length" @change="updateSkuships(checkableSkuships.filter((skuship) => skuship['checked'] !== $event.target.checked), { checked: $event.target.checked })">
              {{ checkedSkuships.length > 0 ? `已选 ${checkedSkuships.reduce((n, skuship) => n + skuship['qty'], 0)}` : '全选' }}
            </label>
          </main>
          <div class="bolder">
            合计:
            <span v-rmb="checkedSkuships.reduce((sum, skuship) => sum + skuship.sku['price'] * skuship['qty'], 0)"></span>
          </div>
          <button type="button" class="w30 btn primary" v-route data-url="/mbr/item/orders/new" :disabled="checkedSkuships.length === 0">
            去结算
          </button>
        </div>
      </template>
    </footer>
    <main class="ctn">
      <div class="expd"></div>
      <template v-if="pageNotEmpty">
        <div class="w-seps">
          <div class="li py-lg" v-for="skuship in checkableSkuships">
            <input class="reset asc fs0" type="checkbox" v-model="skuship.destroyChecked" v-if="destroying">
            <input class="reset asc fs0" type="checkbox" :checked="skuship['checked']" @change="updateSkuship(skuship, { checked: !skuship['checked'] })" v-else>
            <img class="fs0" :src="skuship.sku['image_url']" width="80" height="80" v-route :data-url="mbrItemRoute(skuship)">
            <main>
              <div class="fs-sm bolder" v-route :data-url="mbrItemRoute(skuship)">
                {{ skuship.item['brand_name'] }} · {{ skuship.item['name'] }}
              </div>
              <div class="tag sku-prop-tag c-info fs-xs mt-sm" @click.prevent="currentSkuship = skuship; toggle.itemSku = true; toggle.toggler.itemSku = $event.currentTarget">
                {{ skuship.sku.name() }}
                <span class="icon-chevron-b ml-sm"></span>
              </div>
              <div v-if="skuship.sku['etd']" class="fs-xs c-info mt-sm">
                预计 {{ skuship.sku['etd'] }} 个工作日发货
              </div>
              <div class="li mt-md">
                <main class="bolder" v-rmb="skuship.sku['price']"></main>
                <number-input class="fs-sm" min="1" :max="skuship.sku['qty']" :value="skuship['qty']" @ok="updateSkuship(skuship, { qty: $event }).catch(() => { getItemCart() })"></number-input>
              </div>
              <div class="fs-sm c-danger mt-md tar" v-if="skuship['qty'] > skuship.sku['qty']">剩余库存: {{ skuship.sku['qty'] }}</div>
            </main>
          </div>
        </div>
        <div v-if="uncheckableSkuships.length > 0">
          <div class="w-sep my-lg"></div>
          <div class="li">
            <main class="heading">失效商品 ({{ uncheckableSkuships.length }})</main>
            <button type="button" @click.prevent="destroySkuships(uncheckableSkuships, $event)">清空</button>
          </div>
          <div class="w-seps">
            <div class="li py-lg" v-for="skuship in uncheckableSkuships">
              <div class="tag info fs-xs fs0 asc">失效</div>
              <img class="fs0" :src="skuship.sku['image_url']" width="80" height="80" v-route :data-url="mbrItemRoute(skuship)">
              <main class="c-hint">
                <div class="fs-sm bolder" v-route :data-url="mbrItemRoute(skuship)">
                  {{ skuship.item['brand_name'] }} · {{ skuship.item['name'] }}
                </div>
                <div class="tag sku-prop-tag fs-xs mt-sm">
                  {{ skuship.sku.name() }}
                </div>
                <div class="li mt-md">
                  <main class="bolder" v-rmb="skuship.sku['price']"></main>
                  <div class="fs-xs">{{ skuship['na_reason'] }}</div>
                </div>
              </main>
            </div>
          </div>
        </div>
        <div class="br-footer expd"></div>
      </template>
      <div class="tac" v-else>
        <div class="my-xl py-xl"></div>
        <img src="/images/common/empty.svg" width="164" height="78">
        <div class="fs-sm c-info my-lg">购物车空空如也，快去选购吧~</div>
        <router-link :to="{ name: 'mbr-items-index' }" class="btn major px-xl">去看看</router-link>
      </div>
      <div class="br-footer expd" v-if="!$route.query['backable']"></div>

      <item-sku-select
        :active="toggle.itemSku"
        :toggler="toggle.toggler.itemSku"
        :item-id="currentSkuship['item_id']"
        :sku-id="currentSkuship['sku_id']"
        :sku-qty="currentSkuship['qty']"
        :cart-only="true"
        @close="toggle.itemSku = false"
        @cart="updateSkuship(currentSkuship, { sku_id: $event.sku['id'], qty: $event.qty }).then(() => { currentSkuship.sku = $event.sku })">
      </item-sku-select>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        skuships: [],
        items: [],
        skus: [],
        sku: {
          props: []
        }
      },
      destroying: false,
      currentSkuship: {},
      toggle: {
        itemSku: false,
        toggler: {
          itemSku: null
        }
      }
    }
  },
  mounted() {
    this.getItemCart()
  },
  computed: {
    uncheckableSkuships() {
      return this.res.skuships.filter((skuship) => skuship['na_reason'])
    },
    checkableSkuships() {
      return this.res.skuships.filter((skuship) => !skuship['na_reason'])
    },
    checkedSkuships() {
      return this.checkableSkuships.filter((skuship) => skuship['checked'])
    },
    destroyCheckedSkuships() {
      return this.checkableSkuships.filter((skuship) => skuship.destroyChecked)
    },
    pageNotEmpty() {
      return this.res.skuships.length > 0
    }
  },
  methods: {
    getItemCart() {
      this.ajax('/mbr/item/cart', {}, (response) => {
        response.data.skus.forEach((sku) => { // See item-sku-select
          sku.props = []
          response.data.sku.props.forEach((prop) => {
            if (prop['sku_id'] === sku['id']) {
              sku.props.push(prop)
            }
          })
          sku.name = function (delimiter) {
            return this.props.map((prop) => prop['value']).join(delimiter || ' ')
          }
        })
        response.data.skuships.forEach((skuship) => {
          skuship.destroyChecked = false
          skuship.item = response.data.items.find((item) => item['id'] === skuship['item_id'])
          skuship.sku = response.data.skus.find((sku) => sku['id'] === skuship['sku_id'])
        })
        Object.assign(this.res, response.data)
      })
    },
    updateSkuship(oldSkuship, params) {
      return this.ajax(`/mbr/item/cart/skuships/${oldSkuship['id']}`, {
        method: 'patch',
        data: params
      }, (response) => {
        Object.assign(oldSkuship, response.data)
        this.res.skuships.reduceRight((result, skuship, i) => {
          if (skuship['sku_id'] === oldSkuship['sku_id'] && skuship['id'] !== oldSkuship['id']) {
            this.res.skuships.splice(i, 1)
          }
        }, null)
      })
    },
    updateSkuships(skuships, params = {}) {
      params['ids'] = skuships.map((skuship) => skuship['id'])
      this.ajax('/mbr/item/cart/skuships', {
        method: 'patch',
        data: params
      }, (response) => {
        response.data.skuships.forEach((newSkuship) => {
          Object.assign(this.res.skuships.find((skuship) => skuship['id'] === newSkuship['id']), newSkuship)
        })
      })
    },
    destroySkuships(skuships, event, fn) {
      if (this.confirmDestroy(event)) {
        this.ajax('/mbr/item/cart/skuships', {
          method: 'delete',
          data: {
            ids: skuships.map((skuship) => skuship['id'])
          }
        }, (response) => {
          response.data.ids.forEach((id) => {
            this.res.skuships.splice(this.res.skuships.findIndex((skuship) => skuship['id'] === id), 1)
          })
          if (fn) {
            fn.call(this)
          }
        })
      }
    },
    mbrItemRoute(skuship) {
      return `/mbr/items/${skuship['item_id']}?sku_id=${skuship['sku_id']}&sku_qty=${skuship['qty']}`
    }
  }
}
</script>

<style lang="css" scoped>
.sku-prop-tag {
  --bg: var(--body-bg);
}
</style>
