<template>
  <div class="overlay" :data-active="active" @click="$event.target === $el && emitClose()" @keyup.esc.stop="emitClose()">
    <div class="action-sheet" tabindex="-1" v-autofocus v-if="active">
      <div class="page bg">
        <header class="ctn">
          <button class="l" @click.prevent="emitClose()">
            <span class="icon-x"></span>
          </button>
          <div class="c">{{ title }}</div>
          <slot name="header"></slot>
        </header>
        <footer class="ctn">
          <slot name="footer"></slot>
        </footer>
        <main class="ctn">
          <slot></slot>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: { default: '' },
    active: { default: true },
    toggler: { default: null }
  },
  data() {
    return {
      openTimes: 0
    }
  },
  watch: {
    active() {
      if (this.active) {
        this.emitOpen()
      } else if (this.toggler) {
        this.toggler.focus()
      }
    }
  },
  methods: {
    emitClose() {
      this.$el.dataset.active = false
      this.$emit('close')
    },
    emitOpen() {
      this.openTimes += 1
      this.$emit('open', this.openTimes)
    }
  }
}
</script>
