<template lang="html">
  <a-modal
    :title="res.i18n['address_']"
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="$event === 1 && requestAddresses()">
    <template #header>
      <button ref="newToggler" type="button" class="r" @click="toggle.new = true">添加</button>
    </template>
    <div class="list w-seps w-seps-tb">
      <label class="li" v-for="address in res.addresses">
        <input class="reset asc fs0" type="radio" name="radio" :value="address['id']" :checked="address['id'] == selectedId" data-checkbox @change="emitOk(address)">
        <main class="my-em">
          <div class="bolder">{{ address['name'] }} {{ address['mobile'] }}</div>
          <div class="fs-sm mt-md">
            <span class="badge c-secondary address-defaulted" v-if="address['defaulted']">默认</span>
            <span class="c-info">{{ `${address['province']} ${address['city']} ${address['district']} ${address['street']}` }}</span>
          </div>
        </main>
        <button
          type="button"
          class="fs0 py-em"
          @click="toggle.edit = true; toggle.toggler.edit = $event.currentTarget; editAddressId = address['id']">
          编辑
        </button>
      </label>
    </div>
    <div class="br-footer expd"></div>

    <address-new
      :active="toggle.new"
      :toggler="$refs.newToggler"
      @close="toggle.new = false"
      @ok="requestAddresses()">
    </address-new>

    <address-edit
      :active="toggle.edit"
      :toggler="toggle.toggler.edit"
      :address-id="editAddressId"
      @close="toggle.edit = false"
      @ok="requestAddresses()">
    </address-edit>
  </a-modal>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null },
    selectedId: { default: null }
  },
  data() {
    return {
      res: {
        i18n: {},
        addresses: []
      },
      toggle: {
        new: false,
        edit: false,
        toggler: {
          edit: null
        }
      },
      editAddressId: null
    }
  },
  methods: {
    requestAddresses() {
      this.ajax('/init/addresses').then((response) => {
        this.emitDefault()
        this.emitSelected()
      })
    },
    emitClose() {
      this.$emit('close')
    },
    emitDefault() {
      const address = this.res.addresses.find((address) => address['defaulted'])
      if (address) {
        this.$emit('defaulted', { address: address })
      }
    },
    emitSelected() {
      if (!this.selectedId) {
        return
      }
      const address = this.res.addresses.find((address) => address['id'] === this.selectedId)
      if (address) {
        this.$emit('selected', { address: address })
      }
    },
    emitOk(address) {
      this.emitClose()
      this.$emit('ok', { address: address })
    }
  }
}
</script>
