<template>
  <r-div class="page bg" data-url="/mbr/item/categories" @ok="come">
    <header class="ctn"></header>
    <footer class="ctn">
      <mbr-main-tabs current="category"></mbr-main-tabs>
    </footer>
    <main class="ctn">
      <div class="expd"></div>
      <div class="l1-ctn">
        <label v-for="category in l1Categories" @click.prevent="l1Category = category" class="block fs-lg" :data-active="category === l1Category">
          {{ category['name'] }}
          <div class="br-xl"></div>
        </label>
      </div>
      <div class="list">
        <div class="row gx-sm gy-sm" v-if="l2Categories.length > 0">
          <router-link :to="{ name: 'mbr-items-index', query: { category_id: category['id'] } }" class="col-6 col-md-4 col-lg-3 col-xl-2" v-for="category in l2Categories">
            <img :data-src="category['image_url']" class="block" width="1" height="1">
            <div class="l2-name">{{ category['name'] }}</div>
          </router-link>
        </div>
      </div>
      <div class="br-footer"></div>
    </main>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        categories: []
      },
      l1Category: null,
      l2Categories: []
    }
  },
  computed: {
    l1Categories() {
      return this.res.categories.filter((category) => category['level'] === 'l1')
    }
  },
  watch: {
    l1Category(newCategory, oldCategory) {
      if (newCategory === null) {
        this.l2Categories = []
      } else {
        this.l2Categories = this.res.categories.filter((category) => category['category_id'] === newCategory['id'])
      }
    }
  },
  methods: {
    come(response) {
      Object.assign(this.res, response.data)
      this.l1Category = this.l1Categories[0]
    }
  }
}
</script>

<style lang="scss" scoped>
$l1-width: 90px;
.l1-ctn {
  z-index: 1;
  position: fixed;
  top: var(--header-h);
  width: $l1-width;
  height: calc(100vh - var(--header-h) - var(--footer-h));
  overflow-y: auto;
  label {
    &[data-active] {
      font-weight: bolder;
    }
    &:not([data-active]) {
      --fg: #666;
    }
  }
  + div {
    margin-left: $l1-width + 12px;
  }
}
.l2-name {
  position: absolute;
  top: 10px;
  left: 14px;
  font-size: 14px;
  color: white;
}
</style>
