<template lang="html">
  <a-modal
    :title="`添加${res.i18n['address_']}`"
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="$event === 1 && requestRes()">
    <r-form action="/mbr/user/addresses" @ok="emitOk($event)">
      <div class="w-seps w-seps-tb mb-lg">
        <label class="li">
          <div class="label">{{ res.i18n['name'] }}</div>
          <input class="reset" type="text" name="name" placeholder="请输入" required v-autofocus>
        </label>
        <label class="li">
          <div class="label">{{ res.i18n['mobile'] }}</div>
          <input class="reset" type="tel" name="mobile" placeholder="请输入" required>
        </label>
        <button ref="areaToggler" type="button" class="li w-link" @click.prevent="toggle.area = true">
          <div class="label">{{ res.i18n['district_area_id'] }}</div>
          <div>
            {{ selectedArea.fullName || '请选择' }}
            <input type="hidden" name="district_area_id" :value="selectedArea.districtAreaId">
          </div>
        </button>
        <label class="li">
          <div class="label">{{ res.i18n['street'] }}</div>
          <input class="reset" type="text" name="street" placeholder="请输入" required>
        </label>
        <label class="li">
          <div class="label">{{ res.i18n['defaulted'] }}</div>
          <div>
            <input type="checkbox" name="defaulted" class="reset">
          </div>
        </label>
      </div>
      <div class="br-md"></div>
      <input type="submit" value="确定" class="btn primary block my-lg">
    </r-form>

    <area-select
      :active="toggle.area"
      :toggler="$refs.areaToggler"
      @close="toggle.area = false"
      @ok="selectedArea = $event">
    </area-select>
  </a-modal>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null }
  },
  data() {
    return {
      res: {
        i18n: {}
      },
      selectedArea: {},
      toggle: {
        area: false
      }
    }
  },
  methods: {
    requestRes() {
      this.ajax('/mbr/user/addresses/new')
    },
    emitClose() {
      this.$emit('close')
    },
    emitOk(response) {
      this.emitClose()
      this.$emit('ok', { address: response.data })
    }
  }
}
</script>
