<template>
  <a-dialog @close="$emit('close')">
    <r-div :data-url="'/oa/shop/agencies/' + q.agency.id + '/agency/zones'" @ok="come">
      <main>
        <div class="table-ctn">
          <table>
            <tr>
              <th>{{ i18n.zone }}</th>
              <th></th>
            </tr>
            <tr v-for="(zone, i) in zones">
              <td>{{ zone.area.name }}</td>
              <td>
                <r-a :href="'/oa/shop/agency/zones/' + zone.id" data-method="delete" @ok="afterDestroy(i)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
        <div class="my">
          <r-form class="form row gx" :action="'/oa/shop/agencies/' + q.agency.id + '/agency/zones'" @ok="afterCreate">
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>省</div>
              <select name="province_area_id" required v-model="currentProvinceAreaId" class="border">
                <option value="">请选择</option>
                <option v-for="area in provinceAreas" :value="area.id">{{ area.name }}</option>
              </select>
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>市</div>
              <select name="city_area_id" v-model="currentCityAreaId" class="border">
                <option value="">请选择</option>
                <option v-for="area in cityAreas" :value="area.id">{{ area.name }}</option>
              </select>
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>区/县</div>
              <select name="district_area_id" v-model="currentDistrictAreaId" class="border">
                <option value>请选择</option>
                <option v-for="area in districtAreas" :value="area.id">{{ area.name }}</option>
              </select>
            </label>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <input type="submit" value="添加" class="btn primary block">
            </div>
          </r-form>
        </div>
      </main>
    </r-div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      i18n: {},
      zones: [],
      areas: [],
      currentProvinceAreaId: '',
      currentCityAreaId: '',
      currentDistrictAreaId: ''
    }
  },
  computed: {
    provinceAreas() {
      return this.areas.filter((area) => area.area_id === null && !this.zones.some((zone) => zone.area.id === area.id))
    },
    cityAreas() {
      return this.currentProvinceAreaId ? this.areas.filter((area) => area.area_id === this.currentProvinceAreaId && !this.zones.some((zone) => zone.area.id === area.id)) : []
    },
    districtAreas() {
      return this.currentCityAreaId ? this.areas.filter((area) => area.area_id === this.currentCityAreaId && !this.zones.some((zone) => zone.area.id === area.id)) : []
    },
    newZoneName() {
      const areaId = this.currentDistrictAreaId || this.currentCityAreaId || this.currentProvinceAreaId
      return areaId ? this.areas.find((area) => area.id === areaId).name : ''
    }
  },
  watch: {
    zones(newZones, oldZones) {
      this.currentProvinceAreaId = ''
      this.currentCityAreaId = ''
      this.currentDistrictAreaId = ''
    },
    currentProvinceAreaId() {
      this.currentCityAreaId = ''
    },
    currentCityAreaId() {
      this.currentDistrictAreaId = ''
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.zones = response.data.zones
      this.areas = response.data.areas
    },
    afterCreate(response) {
      this.zones.push({
        id: response.data.id,
        area: {
          id: response.data.area_id,
          name: this.newZoneName
        }
      })
      this.q.agency['zones_cnt'] = this.zones.length
    },
    afterDestroy(i) {
      this.zones.splice(i, 1)
      this.q.agency['zones_cnt'] = this.zones.length
    }
  }
}
</script>
