<template>
  <r-div class="page" data-url="/oa/shops" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th>{{ i18n.id }}</th>
              <th>{{ i18n.name }}</th>
              <th>{{ i18n.tel }}</th>
              <th>{{ i18n.contact_name }}</th>
              <th>{{ i18n.contact_tel }}</th>
              <th>{{ i18n.status }}</th>
              <th>{{ i18n.valid_from }}</th>
              <th>{{ i18n.valid_to }}</th>
              <th><router-link to="/oa/shops/new">新建</router-link></th>
            </tr>
            <tr v-for="(shop, i) in shops">
              <td><router-link :to="{ name: 'oa-shops-show', params: { id: shop.id } }">{{ shop.id }}</router-link></td>
              <td>{{ shop.name }}</td>
              <td>{{ shop.tel }}</td>
              <td>{{ shop.contact_name }}</td>
              <td>{{ shop.contact_tel }}</td>
              <td>{{ statuses[shop.status] }}</td>
              <td v-datetime:date="shop.valid_from"></td>
              <td v-datetime:date="shop.valid_to"></td>
              <td><router-link :to="{ name: 'oa-shops-edit', params: { id: shop.id } }">编辑</router-link></td>
            </tr>
          </table>
        </div>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      shops: {},
      statuses: {}
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.shops = response.data.shops
      this.statuses = response.data.statuses
    }
  }
}
</script>
