<template lang="html">
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">我的收藏</div>
      <button type="button" class="r" @click.prevent="destroying = !destroying">{{ destroying ? '完成' : '编辑' }}</button>
    </header>
    <footer class="ctn w-sep" v-if="destroying">
      <div class="li bg">
        <main class="mdl">
          <label class="py-md"><input class="reset" type="checkbox" :checked="this.checkedNum === this.res.items.length" @change="toggleAll($event)"> {{ this.checkedNum > 0 ? `已选 ${this.checkedNum}` : '全选' }}</label>
        </main>
        <button form="form" class="btn w40 primary" :disabled="checkedNum <= 0">删除</button>
      </div>
    </footer>
    <main class="ctn">
      <div class="list w-seps">
        <label class="li pdy" v-for="item in res.items" v-route :data-url="`/mbr/items/${item['id']}`" :data-routeless="destroying">
          <input form="form" type="hidden" name="bulk[][item_id]" :value="item['id']" v-if="destroying">
          <input form="form" ref="activeChecks" class="reset asc fs0" type="checkbox" name="bulk[][active]" value="false" @change="toggle($event)" v-if="destroying">
          <img class="fs0 " :src="item['image_url']" width="130" height="130">
          <main :class="itemNotActive(item) && 'c-hint'">
            <div class="fs-sm mb-xs">{{ item['brand_name'] }}</div>
            <div class="bolder mb-sm">{{ item['name'] }}</div>
            <div v-if="itemNotActive(item)" class="badge fs-xs hint">失效</div>
            <div v-else class="bolder fa-xl" v-rmb="item['min_price']"></div>
          </main>
        </label>
      </div>
      <div class="w-sep" v-if="res.items.length > 0"></div>
      <r-form id="form" action="/mbr/item/favorites" method="post" @ok="keepOnlyActive($event)"></r-form>
      <div class="br-footer"></div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        items: []
      },
      destroying: false,
      checkedNum: 0
    }
  },
  mounted() {
    this.ajax('/mbr/item/favorites')
  },
  watch: {
    destroying() {
      this.checkedNum = 0
    }
  },
  methods: {
    toggle(event) {
      this.checkedNum = this.$refs.activeChecks.filter((input) => input.checked).length
    },
    toggleAll(event) {
      this.$refs.activeChecks.forEach((input) => {
        input.checked = event.target.checked
      })
      this.checkedNum = event.target.checked ? this.res.items.length : 0
    },
    keepOnlyActive(response) {
      this.res.items = this.res.items.filter((item) => {
        return !response.data.includes(item['id'])
      })
      this.destroying = false
    },
    itemNotActive(item) {
      return item['status'] !== 'active'
    }
  }
}
</script>

