<template>
  <div class="page bg">
    <header :class="['ctn', currentTab == 0 && 'scroll-to-show']">
      <back-link></back-link>
      <div class="c">商品详情</div>
      <div class="subheader cols w-tabs fs-sm">
        <label>
          <input type="radio" name="subheader" value="0" v-model="currentTab">
          <div class="centery">商品</div>
        </label>
        <label>
          <input type="radio" name="subheader" value="1" v-model="currentTab">
          <div class="centery">参数</div>
        </label>
      </div>
    </header>
    <footer class="ctn w-sep" v-show="currentTab == 0">
      <div class="row bg">
        <a href="tel:TODO" class="col-2">
          <img src="/images/common/cs.svg" width="24" height="24" class="centerxy">
        </a>
        <router-link :to="{ name: 'mbr-item-cart-show', query: { backable: true } }" class="col-2">
          <div class="tr-badge centerxy">
            <img src="/images/common/cart.svg" width="24" height="24">
            <div class="badge border round primary fs-xs" v-if="cartSkusQty">{{ cartSkusQty }}</div>
          </div>
        </router-link>
        <button type="button" class="col-8 btn hint" @click.prevent="toggle.areaOrAddress = true; toggle.toggler.areaOrAddress = $event.currentTarget" v-if="unshippable">
          已选地区暂不支持配送
        </button>
        <template v-else-if="!current.sku">
          <button type="button" class="col-4 btn secondary" @click.prevent="toggle.itemSku = true; toggle.toggler.itemSku = $event.currentTarget">
            加入购物车
          </button>
          <button type="button" class="col-4 btn primary" @click.prevent="toggle.itemSku = true; toggle.toggler.itemSku = $event.currentTarget">
            立即购买
          </button>
        </template>
        <template v-else>
          <button type="button" class="col-4 btn secondary" @click.prevent="createItemCartSkuship()">
            加入购物车
          </button>
          <button type="button" class="col-4 btn primary" @click.prevent="newItemOrder()">
            立即购买
          </button>
        </template>
      </div>
    </footer>
    <main class="ctn" v-show="currentTab == 0">
      <div class="row expd">
        <div class="col-lg-6 swiper-container mb">
          <div class="swiper-container">
            <div class="swiper-wrapper ar1-1">
              <div class="swiper-slide" v-for="url in res.item['image_urls']">
                <img :data-src="url" class="block swiper-lazy" width="1" height="1">
                <div class="swiper-lazy-preloader"></div>
              </div>
            </div>
            <div class="swiper-pagination"></div>
          </div>
        </div>
      </div>
      <div class="li mt mb-sm">
        <main>
          <div class="mb-xs bolder">{{ res.item['name'] }}</div>
          <div class="c-info fs-sm">{{ res.item['brand_name'] }}</div>
        </main>
        <label>
          <input type="checkbox" name="active" class="switch" :value="res.favorite" v-model="res.favorite" v-url="'/mbr/item/favorites'" :data-params="`item_id=${$route.params['id']}`">
          <svg width="24" height="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M11.9995 21.3564l9.3522-7.58c2.5143-2.038 2.8733-5.69.803-8.16-2.074-2.4763-5.8019-2.8304-8.318-.79l-1.8371 1.4889-.3149-.2551-1.5209-1.2323c-2.518-2.0425-6.2445-1.689-8.318.7869-2.0704 2.472-1.7124 6.124.8015 8.1616l9.3522 7.58z" stroke="#333" fill-rule="evenodd"/></svg>
        </label>
      </div>
      <div class="flex aic mb">
        <div class="inblock fs-xl bolder mr-md" v-rmb="current.sku ? current.sku['price'] : res.item['min_price']"></div>
        <div class="tag major fs-xs mx-xs" v-for="tagName in res.tags">{{ tagName }}</div>
      </div>
      <dl class="w-seps w-seps-tb">
        <button class="li w-link" @click.prevent="toggle.itemSku = true; toggle.toggler.itemSku = $event.currentTarget">
          <dt class="shift w20">规格</dt>
          <dd class="fg1">{{ current.sku ? `${current.sku.name()} x ${current.skuQty}` : '请选择规格数量' }}</dd>
        </button>
        <button type="button" class="li w-link" @click.prevent="toggle.areaOrAddress = true; toggle.toggler.areaOrAddress = $event.currentTarget">
          <dt class="shift w20">送至</dt>
          <dd class="fg1">{{ currentArea ? currentArea.fullName : '选择配送区域' }}</dd>
        </button>
        <button ref="agencyRemarkToggler" class="li w-link" @click.prevent="toggle.agencyRemark = true" v-if="res.agency['remark']">
          <dt class="shift w20">限制</dt>
          <dd class="fg1">部分地区无法送达，敬请谅解</dd>
        </button>
      </dl>
      <button ref="agencyServicesToggler" class="li w-link py" @click.prevent="toggle.agencyServices = true" v-if="res.agency.services.length > 0">
        <main class="cols">
          <div v-for="service in res.agency.services.slice(0, 3)">
            <img src="/images/mbr/items/service.svg">
            <span>{{ service['name'] }}</span>
          </div>
        </main>
      </button>
      <div class="expd">
        <div v-for="section in res.sections">
          <img :data-src="section['image_url']" class="block" v-if="section['image_url']">
          <p class="mdx mt-md mb-lg" v-if="section['content']">{{ section['content'] }}</p>
        </div>
      </div>
      <div class="br-footer expd"></div>

      <item-sku-select
        :active="toggle.itemSku"
        :toggler="toggle.toggler.itemSku"
        :item-id="itemSkuSelectOpts.itemId"
        :sku-id="itemSkuSelectOpts.skuId"
        :sku-qty="itemSkuSelectOpts.skuQty"
        :sku-only="unshippable"
        @close="toggle.itemSku = false"
        @sku="current.sku = $event.sku; current.skuQty = $event.qty"
        @cart="createItemCartSkuship()"
        @buy="newItemOrder()">
      </item-sku-select>

      <address-select
        v-if="signedIn()"
        :active="toggle.areaOrAddress"
        :toggler="toggle.toggler.areaOrAddress"
        :selected-id="currentArea && currentArea['addressId']"
        @close="toggle.areaOrAddress = false"
        @defaulted="currentArea || populateCurrentArea($event.address)"
        @selected="populateCurrentArea($event.address)"
        @ok="populateCurrentArea($event.address)">
      </address-select>
      <area-select
        v-else
        :active="toggle.areaOrAddress"
        :toggler="toggle.toggler.areaOrAddress"
        :selected-id="currentArea && currentArea['district_area_id']"
        @close="toggle.areaOrAddress = false"
        @ok="currentArea = $event">
      </area-select>

      <a-action-page
        title="限制"
        :active="toggle.agencyRemark"
        :toggler="$refs.agencyRemarkToggler"
        @close="toggle.agencyRemark = false">
        <p class="prel">{{ res.agency['remark'] }}</p>
      </a-action-page>

      <a-action-page
        title="品牌服务"
        :active="toggle.agencyServices"
        :toggler="$refs.agencyServicesToggler"
        @close="toggle.agencyServices = false">
        <div v-for="service in res.agency.services">
          <div class="li">
            <main class="heading">{{ service['name'] }}</main>
          </div>
          <p class="prel c-info mb">{{ service['desc'] }}</p>
        </div>
      </a-action-page>
    </main>

    <main class="ctn" v-show="currentTab == 1">
      <dl class="list w-seps w-seps-tb">
        <div class="li" v-for="prop in res.props">
          <dt class="shift w20">{{ prop['key'] }}</dt>
          <dd>{{ prop['value'] }}</dd>
        </div>
      </dl>
    </main>
  </div>
</template>

<script>
import Swiper from 'swiper'

export default {
  data() {
    return {
      res: {
        item: {},
        tags: [],
        agency: {
          remark: null,
          services: [],
          zones: []
        },
        props: [],
        sections: [],
        favorite: false,
        address: null,
        cart: {}
      },
      current: {
        sku: null,
        skuQty: 0
      },
      swiper: null,
      currentTab: 0,
      itemSkuSelectOpts: {},
      currentArea: null,
      toggle: {
        itemSku: false,
        areaOrAddress: false,
        agencyRemark: false,
        agencyServices: false,
        toggler: {
          itemSku: null,
          areaOrAddress: null
        }
      }
    }
  },
  mounted() {
    this.requestRes()
    const mbrItemsShowArea = localStorage.getItem('mbrItemsShowArea')
    if (mbrItemsShowArea) {
      this.currentArea = JSON.parse(mbrItemsShowArea)
    }
  },
  watch: {
    currentArea() {
      if (this.currentArea) {
        localStorage.setItem('mbrItemsShowArea', JSON.stringify(this.currentArea))
      }
    }
  },
  computed: {
    unshippable() {
      const areaIds = this.res.agency.zones.map((zone) => zone['area_id'])
      return this.currentArea &&
        !areaIds.includes(this.currentArea.districtAreaId) &&
        !areaIds.includes(this.currentArea.cityAreaId) &&
        !areaIds.includes(this.currentArea.provinceAreaId)
    },
    cartSkusQty() {
      return this.res.cart['skus_qty'] ? this.res.cart['skus_qty'] <= 99 ? this.res.cart['skus_qty'] : '99+' : null
    }
  },
  methods: {
    requestRes() {
      this.ajax(`/mbr/items/${this.$route.params['id']}`).then((response) => {
        if (!response.data.item) {
          this.$router.replace({ name: 'mbr-items-missing' })
          return
        }
        if (this.res.address) {
          this.populateCurrentArea(this.res.address)
        }
        this.itemSkuSelectOpts = {
          itemId: this.res.item['id'],
          skuId: this.$route.query['sku_id'],
          skuQty: this.$route.query['sku_qty']
        }
        this.$nextTick(() => this.initSwiper())
      })
    },
    initSwiper() {
      this.swiper = new Swiper('.swiper-container', {
        loop: true,
        lazy: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      })
    },
    populateCurrentArea(address) {
      this.currentArea = {
        addressId: address['id'],
        provinceAreaId: address['province_area_id'],
        cityAreaId: address['city_area_id'],
        districtAreaId: address['district_area_id'],
        fullName: `${address['province']} ${address['city']} ${address['district']}`
      }
    },
    createItemCartSkuship() {
      this.ajax('/mbr/item/cart/skuships', {
        method: 'post',
        data: {
          sku_id: this.current.sku['id'],
          qty: this.current.skuQty
        }
      }).then((response) => {
        this.res.cart['skus_qty'] += this.current.skuQty
      })
    },
    newItemOrder() {
      this.$router.push({
        name: 'mbr-item-orders-new',
        query: {
          sku_id: this.current.sku['id'],
          sku_qty: this.current.skuQty
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.swiper-pagination {
  width: unset;
  left: unset;
  position: absolute;
  right: 0;
  bottom: 20px;
  font-size: 14px;
  padding: 4px 1em;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;
  background-color: rgba(0, 0, 0, .76);
  --fg: rgba(255, 255, 255, .5);
  > :first-child {
    --fg: white;
  }
}
</style>
