<template>
  <div class="overlay" :data-active="active" @click="$event.target === $el && emitClose()" @keyup.esc.stop="emitClose()">
    <div class="dialog ctn bg" tabindex="-1" v-autofocus v-if="active">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null }
  },
  data() {
    return {
      openTimes: 0
    }
  },
  watch: {
    active() {
      if (this.active) {
        this.emitOpen()
      } else if (this.toggler) {
        this.toggler.focus()
      }
    }
  },
  methods: {
    emitClose() {
      this.$el.dataset.active = false
      this.$emit('close')
    },
    emitOpen() {
      this.openTimes += 1
      this.$emit('open', this.openTimes)
    }
  }
}
</script>
