<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" :action="'/oa/item/categories/' + q.category['id'] + '/category/selections'" @ok="go">
          <input type="hidden" name="category_id" :value="q.category['id']">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['select'] }}</div>
            <select name="select_id" value="" class="border" required v-autofocus>
              <option value="">请选择</option>
              <option v-for="select in selects" :value="select['id']">{{ select['name'] }}</option>
            </select>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  computed: {
    selects() {
      const select_ids = this.q.category['selections'].map((selection) => selection['select_id'])
      return this.q.data['selects'].filter((select) => !select_ids.includes(select.id))
    }
  },
  methods: {
    go(response) {
      const selection = response.data
      selection['select'] = this.q.data['selects'].find((select) => select['id'] === selection['select_id'])
      this.q.category['selections'].push(selection)
      this.$emit('close')
    }
  }
}
</script>
