<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" method="patch" :action="'/oa/item/props/' + q.prop['id']" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['prop']['key'] }}</div>
            <input type="text" name="key" :value="q.prop['key']" class="border" required v-placeholder v-autofocus :disabled="q.prop['select_id']">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['prop']['value'] }}</div>
            <input type="text" name="value" :value="q.prop['value']" class="border" required v-placeholder :disabled="q.prop['option_id']">
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="更新" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  methods: {
    go(response) {
      Object.assign(this.q.prop, response.data)
      this.$emit('close')
    }
  }
}
</script>
