import Vue from 'vue'
import router from './concerns/router'
import mixins from './concerns/mixins'
import directives from './concerns/directives'

import oaApp from './pages/oa/app.vue'
import mbrApp from './pages/mbr/app.vue'

import commonComponents from './components/common/*.vue'
import oaComponents from './components/oa/*.vue'
import mbrComponents from './components/mbr/*.vue'

import defaultImage1_1 from './images/common/default1-1.svg'
import defaultImage2_1 from './images/common/default2-1.svg'
import defaultImage4_3 from './images/common/default4-3.svg'
const defaultImages = {
  '1-1': defaultImage1_1,
  '2-1': defaultImage2_1,
  '4-3': defaultImage4_3
}

Vue.config.productionTip = false
Vue.config.devtools = false

for (let name in commonComponents) {
  Vue.component(name, commonComponents[name].default || commonComponents[name])
}
for (let name in oaComponents) {
  Vue.component('oa-' + name, oaComponents[name].default || oaComponents[name])
}
for (let name in mbrComponents) {
  Vue.component('mbr-' + name, mbrComponents[name].default || mbrComponents[name])
}

new Vue({
  router,
  render(createElement) {
    const portal = (this.$route.name || '').split('-')[0]
    return createElement(portal === 'oa' ? oaApp : mbrApp)
  },
  data: {
    windowScrollY: 0,
    lazyImageInterval: 200
  },
  mounted() {
    window.addEventListener('scroll', this.onWindowScroll, false)
    this.loadLazyImages()
  },
  methods: {
    onWindowScroll() {
      this.windowScrollY = window.scrollY
      window.requestAnimationFrame(this.setCssWindowScrollY)

      this.lazyImageInterval = 200
    },
    setCssWindowScrollY() {
      document.documentElement.style.setProperty('--window-scroll-y', this.windowScrollY)
    },
    loadLazyImages() {
      const imgs = document.querySelectorAll('img[data-src]:not(.swiper-lazy)')
      imgs.forEach((img) => {
        if (!img.src) {
          const width = img.width || 1, height = img.height || 1
          const divisor = this.greatestCommonDivisor(width, height)
          img.src = defaultImages[`${width / divisor}-${height / divisor}`] || defaultImage1_1
        }
        const { top, left } = img.getBoundingClientRect()
        if (top <= window.innerHeight + 100 && left <= window.innerWidth + 100) {
          img.src = img.dataset.src
          img.removeAttribute('data-src')
        }
      })
      window.setTimeout(() => {
        this.loadLazyImages()
      }, this.lazyImageInterval)
      if (imgs.length === 0) {
        this.lazyImageInterval = 500
      }
    },
    greatestCommonDivisor(a, b) {
      return b === 0 ? a : this.greatestCommonDivisor(b, a % b)
    }
  }
}).$mount('#app')
