<template lang="html">
  <a-action-page
    title="选择地区"
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="$event === 1 && requestAreas()">
    <template #header>
      <div class="subheader li pdx w-tabs fs-sm">
        <label>
          <input type="radio" name="area" value="province" v-model="tab">
          <div class="centery">{{ provinceArea ? provinceArea['name'] : '省' }}</div>
        </label>
        <label>
          <input type="radio" name="area" value="city" v-model="tab" v-if="provinceArea">
          <div class="centery">{{ cityArea ? cityArea['name'] : '市' }}</div>
        </label>
        <label>
          <input type="radio" name="area" value="district" v-model="tab" v-if="cityArea">
          <div class="centery">{{ districtArea ? districtArea['name'] : '区县' }}</div>
        </label>
      </div>
    </template>
    <div class="list w-seps w-seps-tb" v-if="tab === 'province'">
      <button class="li" v-for="area in provinceAreas" @click.prevent="provinceArea = area; cityArea = districtArea = null; tab = 'city'">
        <dt :class="provinceArea && provinceArea == area && 'c-secondary'">{{ area['name'] }}</dt>
      </button>
    </div>
    <div class="list w-seps w-seps-tb" v-if="tab === 'city'">
      <button class="li" v-for="area in selectableCities()" @click.prevent="cityArea = area; districtArea = null; tab = 'district'">
        <dt :class="cityArea && cityArea == area && 'c-secondary'">{{ area['name'] }}</dt>
      </button>
    </div>
    <div class="list w-seps w-seps-tb" v-if="tab === 'district'">
      <button class="li" v-for="area in selectableDistricts()" @click.prevent="districtArea = area; emitOk()">
        <dt :class="districtArea && districtArea == area && 'c-secondary'">{{ area['name'] }}</dt>
      </button>
    </div>
  </a-action-page>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null },
    selectedId: { default: null }
  },
  data() {
    return {
      res: {
        areas: []
      },
      tab: 'province',
      provinceArea: null,
      cityArea: null,
      districtArea: null
    }
  },
  computed: {
    provinceAreas() {
      return this.res.areas.filter((area) => area['level'] === 'province')
    },
    cityAreas() {
      return this.res.areas.filter((area) => area['level'] === 'city')
    },
    districtAreas() {
      return this.res.areas.filter((area) => area['level'] === 'district')
    },
    currentArea() {
      return {
        provinceAreaId: this.provinceArea['id'],
        cityAreaId: this.cityArea['id'],
        districtAreaId: this.districtArea['id'],
        fullName: `${this.provinceArea['name']} ${this.cityArea['name']} ${this.districtArea['name']}`
      }
    }
  },
  methods: {
    requestAreas() {
      this.ajax('/init/areas').then((response) => {
        this.emitSelected()
      })
    },
    selectableCities() {
      return this.provinceArea ? this.cityAreas.filter((area) => area['area_id'] === this.provinceArea['id']) : []
    },
    selectableDistricts() {
      return this.cityArea ? this.districtAreas.filter((area) => area['area_id'] === this.cityArea['id']) : []
    },
    emitClose() {
      this.$emit('close')
    },
    emitSelected() {
      if (!this.selectedId) {
        return
      }
      this.districtArea = this.districtAreas.find((area) => area['id'] == this.selectedId)
      if (!this.districtArea) {
        return
      }
      this.cityArea = this.cityAreas.find((area) => area['id'] === this.districtArea['area_id'])
      if (!this.cityArea) {
        return
      }
      this.provinceArea = this.provinceAreas.find((area) => area['id'] === this.cityArea['area_id'])
      this.$emit('selected', this.currentArea)
    },
    emitOk() {
      this.emitClose()
      this.$emit('ok', this.currentArea)
    }
  }
}
</script>
