<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" method="patch" :action="'/oa/item/tags/' + q.tag['id']" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['name'] }}</div>
            <input type="text" name="name" :value="q.tag['name']" class="border" v-placeholder v-autofocus>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="更新" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  methods: {
    go(response) {
      Object.assign(this.q.tag, response.data)
      this.$emit('close')
    }
  }
}
</script>
