<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" action="/oa/items" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['categories']['l1'] }}</div>
            <select v-model="l1CategoryId" class="border" required v-autofocus>
              <option value="">请选择</option>
              <option v-for="category in l1Categories()" :value="category.id">{{ category.name }}</option>
            </select>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['categories']['l2'] }}</div>
            <select v-model="l2CategoryId" class="border" required>
              <option value="">请选择</option>
              <option v-for="category in childrenCategories(l1CategoryId)" :value="category.id">{{ category.name }}</option>
            </select>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['categories']['l3'] }}</div>
            <select name="category_id" class="border" required>
              <option value="">请选择</option>
              <option v-for="category in childrenCategories(l2CategoryId)" :value="category.id">{{ category.name }}</option>
            </select>
          </label>
          <div></div>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['shop'] }}</div>
            <select name="shop_id" v-model="shopId" class="border" required>
              <option value="">请选择</option>
              <option v-for="shop in q.shops" :value="shop.id">{{ shop.name }}</option>
            </select>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['brand'] }}</div>
            <select name="brand_id" class="border" required>
              <option value="">请选择</option>
              <option v-for="brand in brands" :value="brand.id">{{ brand.name }}</option>
            </select>
          </label>
          <div></div>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['name'] }}</div>
            <input type="text" name="name" value="" class="border" required v-placeholder>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['dri'] }}</div>
            <input type="number" name="dri" value="0" class="border" required v-placeholder>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['hri'] }}</div>
            <input type="number" name="hri" value="0" class="border" required v-placeholder>
          </label>
          <div></div>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['image'] }}</div>
            <input type="file" name="image" class="border" required v-placeholder>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.i18n['images'] }}</div>
            <input type="file" multiple name="images[]" class="border" required v-placeholder>
          </label>
          <div></div>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      l1CategoryId: '',
      l2CategoryId: '',
      shopId: ''
    }
  },
  computed: {
    brands() {
      if (this.shopId) {
        return this.q.shops.find((shop) => shop['id'] === this.shopId)['brands']
      } else {
        return []
      }
    }
  },
  methods: {
    go(response) {
      this.q.items.push(response.data)
      this.$emit('close')
    },
    l1Categories() {
      return this.q.categories.filter((category) => category['level'] === 'l1')
    },
    childrenCategories(id) {
      return this.q.categories.filter((category) => category['category_id'] === id)
    }
  }
}
</script>
