<template>
  <r-div class="page" :data-url="'/oa/shops/' + $route.params.id" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n.id }}</dt>
                <dd>{{ shop.id }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.name }}</dt>
                <dd>{{ shop.name }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.tel }}</dt>
                <dd>{{ shop.tel }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.addr }}</dt>
                <dd>{{ shop.addr }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.open_time }}</dt>
                <dd>{{ shop.open_time }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.closed_time }}</dt>
                <dd>{{ shop.closed_time }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.created_at }}</dt>
                <dd v-datetime="shop.created_at"></dd>
              </div>
              <div class="li">
                <dt>{{ i18n.updated_at }}</dt>
                <dd v-datetime="shop.updated_at"></dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n.contact_name }}</dt>
                <dd>{{ shop.contact_name }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.contact_tel }}</dt>
                <dd>{{ shop.contact_tel }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.status }}</dt>
                <dd>{{ statuses[shop.status] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.valid_from }}</dt>
                <dd v-datetime="shop.valid_from"></dd>
              </div>
              <div class="li">
                <dt>{{ i18n.valid_to }}</dt>
                <dd v-datetime="shop.valid_to"></dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n.bankcard_number }}</dt>
                <dd>{{ shop.bankcard_number }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.bankcard_account }}</dt>
                <dd>{{ shop.bankcard_account }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n.bankcard_name }}</dt>
                <dd>{{ shop.bankcard_name }}</dd>
              </div>
            </dl>
          </div>
        </div>
        <div class="table-ctn my">
          <table>
            <tr>
              <th>{{ i18n.brand }}</th>
              <th>{{ i18n.service }}</th>
              <th>{{ i18n.zone }}</th>
              <th>{{ i18n.remark }}</th>
              <th></th>
            </tr>
            <tr v-for="(agency, i) in agencies">
              <td>{{ agency.brand_name }}</td>
              <td><a href="#" @click.prevent="component = 'oa-shop-agency-services'; componentQuery = { agency: agency }">{{ agency.services_cnt }}</a></td>
              <td><a href="#" @click.prevent="component = 'oa-shop-agency-zones'; componentQuery = { agency: agency }">{{ agency.zones_cnt }}</a></td>
              <td>{{ agency.remark }}</td>
              <td>
                <a href="#" @click.prevent="beforeBrandEdit(i)">编辑</a>
                <r-a :href="'/oa/shop/agencies/' + agency.id" data-method="delete" @ok="agencies.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
        <div class="my" v-if="!editAgency">
          <r-form class="form row gx" :action="'/oa/shops/' + $route.params.id + '/shop/agencies'" @ok="afterBrandCreate">
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.brand }}</div>
              <select name="brand_id" required v-model="newAgency.brand_id">
                <option value="">请选择</option>
                <option v-for="brand in selectableBrands" :value="brand.id">{{ brand.name }}</option>
              </select>
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.remark }}</div>
              <input type="text" name="remark" v-model="newAgency.remark">
            </label>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <input type="submit" value="添加" class="btn primary block">
            </div>
          </r-form>
        </div>
        <div class="my" v-if="editAgency">
          <r-form class="form row gx" method="patch" :action="'/oa/shop/agencies/' + editAgency.id" @ok="afterBrandUpdate">
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.brand }}</div>
              <input type="text" :value="editAgency.brand_name" disabled>
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.remark }}</div>
              <input type="text" name="remark" :value="editAgency.remark">
            </label>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <input type="submit" value="更新" class="btn primary block">
            </div>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <button class="btn c-info block" @click.prevent="afterBrandCancel">取消</button>
            </div>
          </r-form>
        </div>
        <component :is="component" :q="componentQuery" @close="afterComponentClose"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      shop: {},
      brands: [],
      agencies: [],
      statuses: [],
      toggle: {
        agencyForm: 'new'
      },
      newAgency: {
        brand_id: '',
        remark: null
      },
      editAgency: null,
      component: null,
      componentQuery: null
    }
  },
  computed: {
    selectableBrands() {
      return this.brands.filter((brand) => !this.agencies.some((agency) => agency.brand_id == brand.id))
    }
  },
  methods: {
    afterComponentClose() {
      this.component = null
    },
    come(response) {
      this.i18n = response.data.i18n
      this.shop = response.data.shop
      this.brands = response.data.brands
      this.agencies = response.data.agencies
      this.statuses = response.data.statuses
    },
    afterBrandCreate(response) {
      let brand = this.brands.find((brand) => brand.id == this.newAgency.brand_id)
      this.agencies.push({
        id: response.data.id,
        brand_id: brand.id,
        brand_name: brand.name,
        services_cnt: 0,
        zones_cnt: 0,
        remark: this.newAgency.remark
      })
      this.newAgency.brand_id = ''
      this.newAgency.remark = ''
    },
    beforeBrandEdit(i) {
      this.editAgency && this.agencies.push(this.editAgency)
      this.editAgency = this.agencies.splice(i, 1)[0]
    },
    afterBrandUpdate(response) {
      this.editAgency.remark = response.data.remark
      this.agencies.push(this.editAgency)
      this.editAgency = null
    },
    afterBrandCancel() {
      this.agencies.push(this.editAgency)
      this.editAgency = null
    }
  }
}
</script>
