<template>
  <r-div class="page" data-url="/oa/item/selects" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-6">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['select'] }}</dt>
                <dd><a href="#" @click.prevent="component = 'oa-item-selects-new'; componentQuery = { data: $data }">添加</a></dd>
              </div>
              <div class="li" v-for="(select, i) in selects">
                <dt>#{{ select['id'] }} {{ select['name'] }}</dt>
                <dd>
                  <a href="#" @click.prevent="currentSelect = select">{{ i18n['option'] }}</a>
                  <a href="#" @click.prevent="component = 'oa-item-selects-edit'; componentQuery = { data: $data, select: select }">编辑</a>
                  <r-a :href="'/oa/item/selects/' + select.id" data-method="delete" @ok="selects.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6">
            <dl class="px bg">
              <div class="li">
                <dt>{{ currentSelect && currentSelect.name || i18n['option'] }}</dt>
                <dd><a href="#" @click.prevent="component = 'oa-item-options-new'; componentQuery = { data: $data, select: currentSelect }" v-if="currentSelect">添加</a></dd>
              </div>
              <div class="li" v-for="(option, i) in currentOptions" v-if="currentSelect">
                <dt>#{{ option['id'] }} {{ option['name'] }}</dt>
                <dd>
                  <a href="#" @click.prevent="component = 'oa-item-options-edit'; componentQuery = { data: $data, option: option }">编辑</a>
                  <r-a :href="'/oa/item/options/' + option.id" data-method="delete" @ok="afterOptionDestroy(option)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      selects: [],
      options: [],
      component: null,
      componentQuery: null,
      currentSelect: null
    }
  },
  computed: {
    currentOptions() {
      return this.currentSelect ? this.options.filter((option) => option['select_id'] === this.currentSelect['id']) : []
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.selects = response.data['selects']
      this.options = response.data['options']
    },
    afterOptionDestroy(destroyedOption) {
      this.options.splice(this.options.findIndex((option) => option.id === destroyedOption.id), 1)
    }
  }
}
</script>
