<template lang="html">
  <div class="page bg">
  </div>
</template>

<script>
import Wechat from '/concerns/wechat.js'

export default {
  mounted() {
    if (this.$route.query['code']) {
      this.fetchOpenid(this.$route.query['code'])
    } else {
      this.fetchCodeUrl()
    }
  },
  computed: {
    redirection() {
      return this.$route.query['redirection'] || '/mbr'
    }
  },
  methods: {
    fetchCodeUrl() {
      this.ajax(`/wechat/oap/code_url?url=${location.href}`).then((response) => {
        location.href = response.data['url']
      }).catch((error) => {
        this.$router.replace(this.redirection)
      })
    },
    fetchOpenid(code) {
      this.ajax(`/wechat/oap/openid?code=${code}`).finally(() => {
        this.$router.replace(this.redirection)
      })
    }
  }
}
</script>
