<template>
  <r-div class="page" data-url="/oa/op/topics" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th>{{ i18n.id }}</th>
              <th>{{ i18n.name }}</th>
              <th>{{ i18n.dri }}</th>
              <th><router-link to="/oa/op/topics/new">新建</router-link></th>
            </tr>
            <tr v-for="(topic, i) in topics">
              <td><router-link :to="{ name: 'oa-op-topics-show', params: { id: topic.id } }">{{ topic.id }}</router-link></td>
              <td>{{ topic.name }}</td>
              <td>{{ topic.dri }}</td>
              <td>
                <router-link :to="{ name: 'oa-op-topics-edit', params: { id: topic.id } }">编辑</router-link>
                <r-a :href="'/oa/op/topics/' + topic.id" data-method="delete" @ok="topics.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      topics: {}
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.topics = response.data.topics
    }
  }
}
</script>
