export default {
  oap: {
    fromWechat() {
      const regex = /MicroMessenger/i
      return regex.test(navigator.userAgent)
    },
    openidPresent() {
      return document.cookie.split(';').some((item) => item.trim().startsWith('wechat_openid='))
    },
    openidFetchable() {
      if (this.fromWechat() && !this.openidPresent()) {
        const now = new Date().getTime()
        const then = sessionStorage.getItem('openid_fetched_at') || 0
        if (then === 0 || now - then > 60000) { // Fetch at the beginning, or 1 minute later.
          sessionStorage.setItem('openid_fetched_at', now)
          return true
        }
      }
      return false
    }
  }
}
