<template>
  <a class="l" :href="href" @click.prevent="back()">
    <div :class="icon" v-if="icon"></div>
    <slot></slot>
  </a>
</template>

<script>
export default {
  props: {
    href: {
      default: ''
    },
    icon: {
      default: 'icon-chevron-l'
    }
  },
  methods: {
    back() {
      const originalUrl = location.href
      this.$router.go(-1)
      setTimeout(() => {
        if (location.href === originalUrl) {
          this.$router.replace(this.href || '/mbr')
        }
      }, 100)
    }
  }
}
</script>

<style scoped>
.icon-chevron-l {
  --stroke-width: 2px;
}
</style>
