<template>
  <div class="flex">
    <button type="button" :disabled="num <= minValue" @click.prevent="emitOk(num - stepValue)"></button>
    <input type="number" :id="id" :name="name" :value="num" :min="min" :max="max" :step="step" @change="emitOk(parseInt($event.target.value) || 0)">
    <button type="button" :disabled="num >= maxValue" @click.prevent="emitOk(num + stepValue)"></button>
  </div>
</template>

<script>
export default {
  props: ['id', 'name', 'value', 'min', 'max', 'step'],
  data() {
    return {
      num: this.value || 0
    }
  },
  watch: {
    value(newValue) {
      this.num = parseInt(newValue)
    }
  },
  computed: {
    minValue() {
      return (this.min === void 0 || this.min === null) ? -Infinity : parseInt(this.min)
    },
    maxValue() {
      return (this.max === void 0 || this.max === null) ? Infinity : parseInt(this.max)
    },
    stepValue() {
      return (this.step === void 0 || this.step === null) ? 1 : parseInt(this.step)
    }
  },
  methods: {
    emitOk(newValue) {
      if (newValue < this.minValue) {
        this.num = null
        this.emitOk(this.minValue)
      } else if (newValue > this.maxValue) {
        this.num = null
        this.emitOk(this.maxValue)
      } else {
        this.num = newValue
        this.$emit('ok', this.num)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
$h: 1.8em;
.flex {
  color: var(--hint);
  border: 1px solid;
  > * {
    width: calc(#{$h} + 1px);
    height: calc(#{$h} + 1px);
  }
  &:before, &:after, > :first-child:after, > :last-child:after, > :last-child:before {
    content: '';
    display: block;
    position: absolute;
    background-color: currentColor;
  }
  &:before, &:after {
    width: 1px;
    height: calc(#{$h / 2} + 1px);
    top: $h / 4;
  }
  &:before {
    z-index: 1;
    left: $h;
  }
  &:after {
    right: $h;
  }
  > :first-child:after, > :last-child:after {
    width: calc(#{$h / 2} + 1px);
    height: 1px;
    left: $h / 4;
    top: $h / 2;
  }
  > :last-child:before {
    width: 1px;
    height: calc(#{$h / 2} + 1px);
    left: $h / 2;
    top: $h / 4;
  }
  > button:disabled {
    color: var(--na);
  }
  > input {
    width: $h * 2;
    text-align: center;
    border: none;
  }
}
</style>
