<template>
  <r-div class="page bg" data-url="/mbr" @ok="come">
    <header class="ctn">
      <div class="c">
        <img src="/images/mbr/main/header.svg" width="80" height="18">
      </div>
    </header>
    <footer class="ctn">
      <mbr-main-tabs current="home"></mbr-main-tabs>
    </footer>
    <main class="ctn">
      <div class="row expd">
        <div class="col-lg-6 swiper-container mb">
          <div class="swiper-wrapper ar4-3">
            <router-link :to="banner['url']" class="swiper-slide" v-for="banner in res.banners">
              <img :data-src="banner['image_url']" class="block swiper-lazy" width="4" height="3">
              <div class="swiper-lazy-preloader"></div>
            </router-link>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
      <div class="my" v-for="topic in res.topics">
        <div class="li">
          <main class="heading">{{ topic['name'] }}</main>
          <router-link :to="{ name: 'mbr-op-topics-show', params: { id: topic['id'] } }" class="w-link">更多</router-link>
        </div>
        <div class="row gx-sm gy-md">
          <router-link :to="subtopic['url']" class="col-6 col-md-4 col-lg-3 col-xl-2 inh-fg" v-for="subtopic in topic['ordered_subtopics']">
            <img :data-src="subtopic['image_url']" class="block" width="2" height="1">
            <div class="my-md tac fs-sm">{{ subtopic['name'] }}</div>
          </router-link>
        </div>
      </div>
      <div class="w-sep"></div>
      <div class="my">
        <div class="li">
          <main class="heading">谊选好物推荐</main>
        </div>
        <div class="row gx-sm gy-lg">
          <router-link :to="{ name: 'mbr-items-show', params: { id: item['id'] } }" class="col-6 col-md-4 col-lg-3 col-xl-2 inh-fg" v-for="item in res.items">
            <img :data-src="item['image_url']" class="block" width="1" height="1">
            <div class="pr-em">
              <div class="mt-md fs-xs c-info">{{ item['brand_name'] }}</div>
              <div class="my-sm bolder">{{ item['name'] }}</div>
              <div class="mb-xs fs-xl bolder" v-rmb="item['min_price']"></div>
              <div class="fs-xs" v-if="res.tags[item['id']]">
                <div class="tag major mx-xs my-xs" v-for="tagName in res.tags[item['id']]">{{ tagName }}</div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
      <div class="br-footer"></div>
    </main>
  </r-div>
</template>

<script>
import Swiper from 'swiper'

export default {
  data() {
    return {
      res: {
        i18n: {},
        banners: null,
        topics: null,
        items: null,
        tags: null
      },
      swiper: null
    }
  },
  activated() {
    this.$nextTick(() => this.initSwiper())
  },
  methods: {
    come(response) {
      Object.assign(this.res, response.data)
      this.$nextTick(() => this.initSwiper())
    },
    initSwiper() {
      if (this.swiper) {
        this.swiper.destroy()
      }
      this.swiper = new Swiper('.swiper-container', {
        loop: true,
        lazy: true,
        autoplay: {
          delay: 5000
        },
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.swiper-container {
  border-bottom-left-radius: 38px;
  overflow: hidden;
  .swiper-pagination {
    width: unset;
    left: unset;
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 14px;
    padding: 4px 1em;
    border-top-left-radius: 16px;
    background-color: rgba(0, 0, 0, .76);
    --fg: rgba(255, 255, 255, .5);
    > :first-child {
      --fg: white;
    }
  }
}
</style>
