<template>
  <div :data-loading="loading"><slot></slot></div>
</template>

<script>
import axios from '/concerns/axios'

export default {
  data() {
    return {
      loading: false
    }
  },
  mounted() {
    this.loading = this.$el.dataset.loadingName || 'fullscreen'
    axios({
      method: this.$el.dataset.method || 'get',
      url: this.$el.dataset.url,
      data: this.$el.dataset.params
    }).then((response) => {
      this.$emit('ok', response)
    }).catch((error) => {
      this.handleError(error)
      this.$emit('fail', error)
    }).then(() => {
      this.loading = false
    })
  }
}
</script>
