<template lang="html">
  <r-div class="page bg" data-url="/mbr/user/edit" @ok="come">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">修改{{ res.i18n[$route.query['only']] }}</div>
    </header>
    <main class="ctn">
      <r-form class="row" action="/mbr/user" method="patch" @ok="$router.go(-1)">
        <div class="col-md-6">
          <input type="text" name="nickname" :value="res.user['nickname']" :placeholder="`请输入${res.i18n['nickname']}`" class="input" required v-autofocus v-if="$route.query['only'] === 'nickname'">
          <select class="input" required name="gender" v-else-if="$route.query['only'] === 'gender'">
            <option v-for="(genderName, gender) in res.genders" :value="gender" :selected="gender === res.user['gender']">{{ genderName }}</option>
          </select>
          <input type="date" name="member_attributes[birthday]" :value="res.member['birthday']" :placeholder="`请输入${res.i18n['birthday']}`" class="input" required v-autofocus v-else-if="$route.query['only'] === 'birthday'">
          <div class="br-md"></div>
          <input type="submit" value="确定" class="btn primary block my-lg">
        </div>
      </r-form>
    </main>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        i18n: {},
        user: {},
        member: {},
        genders: []
      }
    }
  },
  methods: {
    come(response) {
      Object.assign(this.res, response.data)
    }
  }
}
</script>
