<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" action="/oa/item/options" @ok="go">
          <input type="hidden" name="select_id" :value="q.select['id']">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['name'] }}</div>
            <input type="text" name="name" class="border" v-placeholder v-autofocus>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  methods: {
    go(response) {
      this.q.data.options.push(response.data)
      this.$emit('close')
    }
  }
}
</script>
