<template lang="html">
  <a-modal
    :title="`编辑${res.i18n['address_']}`"
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="requestRes()">
    <template #header>
      <r-a
        class="r"
        :href="`/mbr/user/addresses/${addressId}`"
        data-method="delete"
        data-confirm-destroy
        @ok="emitOk($event)">
        删除
      </r-a>
    </template>

    <r-form :action="`/mbr/user/addresses/${addressId}`" method="patch" @ok="emitOk($event)">
      <div class="w-seps w-seps-tb mb-lg">
        <label class="li">
          <div class="label">{{ res.i18n['name'] }}</div>
          <input class="reset" type="text" name="name" :value="res.address['name']" placeholder="请输入" required v-autofocus>
        </label>
        <label class="li">
          <div class="label">{{ res.i18n['mobile'] }}</div>
          <input class="reset" type="tel" name="mobile" :value="res.address['mobile']" placeholder="请输入" required>
        </label>
        <button ref="areaToggler" type="button" class="li w-link" @click.prevent="toggle.area = true">
          <div class="label">{{ res.i18n['district_area_id'] }}</div>
          <div>
            {{ selectedArea.fullName || res.address['area_full_name'] }}
            <input type="hidden" name="district_area_id" :value="selectedArea.districtAreaId">
          </div>
        </button>
        <label class="li">
          <div class="label">{{ res.i18n['street'] }}</div>
          <input class="reset" type="text" name="street" :value="res.address['street']" placeholder="请输入" required>
        </label>
        <label class="li">
          <div class="label">{{ res.i18n['defaulted'] }}</div>
          <div>
            <input type="checkbox" name="defaulted" class="reset" :checked="res.address['defaulted']" :disabled="res.address['defaulted']">
          </div>
        </label>
      </div>
      <div class="br-md"></div>
      <input type="submit" value="确定" class="btn primary block my-lg">
    </r-form>

    <area-select
      :active="toggle.area"
      :toggler="$refs.areaToggler"
      @close="toggle.area = false"
      @ok="selectedArea = $event">
    </area-select>
  </a-modal>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null },
    addressId: { default: null }
  },
  data() {
    return {
      res: {
        i18n: {},
        address: {}
      },
      selectedArea: {},
      toggle: {
        area: false
      }
    }
  },
  methods: {
    requestRes() {
      this.ajax(`/mbr/user/addresses/${this.addressId}/edit`)
    },
    emitClose() {
      this.$emit('close')
    },
    emitOk(response) {
      this.emitClose()
      this.$emit('ok')
    }
  }
}
</script>
