<template>
  <a-dialog @close="$emit('close')">
    <r-div :data-url="'/oa/shop/agencies/' + q.agency.id + '/agency/services'" @ok="come">
      <main>
        <div class="table-ctn">
          <table>
            <tr>
              <th>{{ i18n.name }}</th>
              <th>{{ i18n.desc }}</th>
              <th>{{ i18n.dri }}</th>
              <th></th>
            </tr>
            <tr v-for="(service, i) in services">
              <td>{{ service.name }}</td>
              <td>{{ service.desc }}</td>
              <td>{{ service.dri }}</td>
              <td>
                <a href="#" @click.prevent="beforeEdit(i)">编辑</a>
                <r-a :href="'/oa/shop/agency/services/' + service.id" data-method="delete" @ok="afterDestroy(i)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
        <div class="my" v-if="!editService">
          <r-form class="form row gx" :action="'/oa/shop/agencies/' + q.agency.id + '/agency/services'" @ok="afterCreate">
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.name }}</div>
              <input type="text" name="name" v-model="newService.name" required class="border">
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.desc }}</div>
              <input type="text" name="desc" v-model="newService.desc" required class="border">
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.dri }}</div>
              <input type="number" name="dri" v-model="newService.dri" required class="border">
            </label>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <input type="submit" value="添加" class="btn primary block">
            </div>
          </r-form>
        </div>
        <div class="my" v-if="editService">
          <r-form class="form row gx" :action="'/oa/shop/agency/services/' + editService.id" method="patch" @ok="afterUpdate">
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.name }}</div>
              <input type="text" name="name" :value="editService.name" required class="border">
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.desc }}</div>
              <input type="text" name="desc" :value="editService.desc" required class="border">
            </label>
            <label class="col-md-6 col-lg-4 col-xl-3">
              <div v-label>{{ i18n.dri }}</div>
              <input type="number" name="dri" :value="editService.dri" required class="border">
            </label>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <input type="submit" value="更新" class="btn primary block">
            </div>
            <div class="col-md-6 col-lg-4 col-xl-3">
              <button class="btn c-info block" @click.prevent="afterCancel">取消</button>
            </div>
          </r-form>
        </div>
      </main>
    </r-div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      i18n: {},
      services: [],
      newService: {
        dri: 0
      },
      editService: null
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.services = response.data.services
    },
    afterCreate(response) {
      this.services.push(response.data)
      this.newService.name = ''
      this.newService.desc = ''
      this.newService.dri = 0
      this.q.agency['services_cnt'] = this.services.length
    },
    afterDestroy(i) {
      this.services.splice(i, 1)
      this.q.agency['services_cnt'] = this.services.length
    },
    beforeEdit(i) {
      this.editService && this.services.push(this.editService)
      this.editService = this.services.splice(i, 1)[0]
    },
    afterUpdate(response) {
      Object.assign(this.editService, response.data)
      this.services.push(this.editService)
      this.editService = null
      this.q.agency['services_cnt'] = this.services.length
    },
    afterCancel() {
      this.services.push(this.editService)
      this.editService = null
    }
  }
}
</script>
