<template>
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">订单确认</div>
    </header>
    <footer class="ctn w-sep">
      <div class="li bg">
        <main class="pdl">
          {{ res.i18n.order['cost'] }}: <span v-rmb="res.order['cost']"></span>
        </main>
        <button
          v-if="addressPresent"
          type="submit"
          form="form"
          class="btn primary w30"
          :disabled="!!res.order['error']">
          确认订单
        </button>
        <button
          v-else
          type="button"
          class="btn primary w40"
          @click="selectAddress($event.currentTarget)">
          添加{{ res.i18n['address_'] }}
        </button>
      </div>
    </footer>
    <main class="ctn">
      <button type="button" class="li w-link pb" @click="selectAddress($event.currentTarget)">
        <main>
          <div class="fs-lg bolder mb-md">{{ res.i18n['address_'] }}</div>
          <template v-if="addressPresent">
            <div>
              {{ res.order['address_name'] }}
              {{ res.order['address_mobile'] }}
            </div>
            <div class="mt-xs">
              {{ res.order['address_province'] }}
              {{ res.order['address_city'] }}
              {{ res.order['address_district'] }}
              {{ res.order['address_street'] }}
            </div>
          </template>
          <div v-else>添加</div>
        </main>
      </button>
      <div class="w-sep expd"></div>
      <div class="fs-lg bolder mt-lg mb-md">{{ res.i18n['skuship_'] }}</div>
      <div class="w-sep"></div>
      <div class="list w-seps">
        <div class="py" v-for="skuship in groupedSkuships">
          <input form="form" type="hidden" :name="`sku_opts[${skuship['sku_id']}]`" :value="skuship['qty']">
          <div class="li">
            <img class="fs0" :data-src="skuship['image_url']" width="80" height="80">
            <div class="fg1">
              <div class="fs-sm bolder">
                {{ skuship['brand_name'] }} · {{ skuship['item_name'] }}
              </div>
              <div class="fs-xs mt-sm">
                {{ skuship['propships'].map((propship) => propship['value']).join(' ') }}
              </div>
              <div class="fs-xs mt-sm" v-if="skuship['etd']">
                预计 {{ skuship['etd'] }} 个工作日发货
              </div>
              <div class="li mt-lg">
                <main class="bolder" v-rmb="skuship['price']"></main>
                <div class="fs-sm">x {{ skuship['qty'] }}</div>
              </div>
            </div>
          </div>
          <div class="fs-sm c-danger mt-md prel" v-if="skuship['error']">{{ skuship['error'] }}</div>
        </div>
      </div>
      <div class="w-sep expd"></div>
      <div class="fs-lg bolder mt-lg mb-md">价格明细</div>
      <div class="w-sep"></div>
      <dl class="w-seps">
        <div class="li">
          <dt>{{ res.i18n.order['price'] }}</dt>
          <dd v-rmb="res.order['price']"></dd>
        </div>
        <div class="li">
          <dt>{{ res.i18n.order['shipping_price'] }}</dt>
          <dd v-rmb="res.order['shipping_price']"></dd>
        </div>
        <div class="li">
          <dt>{{ res.i18n.order['cost'] }}</dt>
          <dd v-rmb="res.order['cost']"></dd>
        </div>
      </dl>
      <div class="w-sep expd"></div>
      <div class="fs-lg bolder mt-lg mb-md">{{ res.i18n.order['remark'] }}</div>
      <r-form id="form" action="/mbr/item/orders" @ok="$router.replace(`/mbr/item/orders/${$event.data['id']}`)" @fail="requestNew()">
        <input type="hidden" name="address_id" :value="res.order['address_id']">
        <textarea name="remark" rows="4" class="reset border block" placeholder="填写您的备注信息，如期望送装时间等"></textarea>
      </r-form>
      <div class="br-footer"></div>

      <address-select
        :active="toggle.addressSelect"
        :toggler="toggle.toggler.addressSelect"
        :selected-id="queryAddressId"
        @close="toggle.addressSelect = false"
        @ok="queryAddressId = $event.address['id']; requestNew()">
      </address-select>

      <address-new
        :active="toggle.addressNew"
        :toggler="toggle.toggler.addressNew"
        @close="toggle.addressNew = false"
        @ok="queryAddressId = $event.address['id']; requestNew()">
      </address-new>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        i18n: {
          order: {}
        },
        order: {
          skuships: []
        }
      },
      toggle: {
        addressSelect: false,
        addressNew: false,
        toggler: {
          addressSelect: null,
          addressNew: null
        }
      },
      queryAddressId: this.$route.query['address_id']
    }
  },
  mounted() {
    this.requestNew()
  },
  computed: {
    groupedSkuships() {
      return Array.from(this.res.order.skuships.reduce((result, skuship) => {
        if (result.has(skuship['group_code'])) {
          result.get(skuship['group_code'])['qty'] += 1
        } else {
          result.set(skuship['group_code'], skuship)
          result.get(skuship['group_code'])['qty'] = 1
        }
        return result
      }, new Map()).values())
    },
    addressPresent() {
      return !!this.res.order['address_id']
    }
  },
  methods: {
    requestNew() {
      this.ajax('/mbr/item/orders/new', {
        params: {
          address_id: this.queryAddressId,
          sku_id: this.$route.query['sku_id'],
          sku_qty: this.$route.query['sku_qty']
        }
      }).then(() => {
        this.queryAddressId = this.res.order['address_id']
      })
    },
    selectAddress(target) {
      if (this.addressPresent) {
        this.toggle.addressSelect = true
        this.toggle.toggler.addressSelect = target
      } else {
        this.toggle.addressNew = true
        this.toggle.toggler.addressNew = target
      }
    }
  }
}
</script>
