<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" :action="'/oa/items/' + q.data.item['id'] + '/item/props'" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['select'] }}</div>
            <select name="select_id" v-model="select_id" class="border" v-autofocus>
              <option value="">请选择</option>
              <option v-for="select in q.data.selects" :value="select['id']">{{ select['name'] }}</option>
            </select>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['option'] }}</div>
            <select name="option_id" v-model="option_id" class="border">
              <option value="">请选择</option>
              <option v-for="option in options" :value="option['id']">{{ option['name'] }}</option>
            </select>
          </label>
          <div></div>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['prop']['key'] }}</div>
            <input type="text" name="key" class="border" required v-placeholder :disabled="!!select_id">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['prop']['value'] }}</div>
            <input type="text" name="value" class="border" required v-placeholder :disabled="!!option_id">
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="创建" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      select_id: '',
      option_id: ''
    }
  },
  computed: {
    options() {
      return this.select_id ? this.q.data.options.filter((option) => option.select_id === this.select_id) : []
    }
  },
  methods: {
    go(response) {
      this.q.data.item.props.push(response.data)
      this.$emit('close')
    }
  }
}
</script>
