<template>
  <r-div class="page" data-url="/oa/item/tags" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-6">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['model'] }}</dt>
                <dd><a href="#" @click.prevent="component = 'oa-item-tags-new'; componentQuery = { data: $data }">添加</a></dd>
              </div>
              <div class="li" v-for="(tag, i) in tags">
                <dt>#{{ tag['id'] }} {{ tag['name'] }}</dt>
                <dd>
                  <a href="#" @click.prevent="component = 'oa-item-tags-edit'; componentQuery = { data: $data, tag: tag }">编辑</a>
                  <r-a :href="'/oa/item/tags/' + tag.id" data-method="delete" @ok="tags.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      tags: [],
      component: null,
      componentQuery: null
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.tags = response.data['tags']
    }
  }
}
</script>
