<template>
  <a-dialog
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="emitOpen()">
    <main>
      <p class="prel">{{ msg }}</p>
    </main>
    <div class="cols">
      <button type="button" class="btn block body-bg" @click.prevent="emitCancel()">取消</button>
      <button type="button" class="btn block primary" @click.prevent="emitOk()">确定</button>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: {
    msg: { default: null },
    active: { default: true },
    toggler: { default: null }
  },
  methods: {
    emitClose() {
      this.$emit('close')
    },
    emitOpen() {
      this.$emit('open')
    },
    emitCancel() {
      this.emitClose()
      this.$emit('cancel')
    },
    emitOk() {
      this.emitClose()
      this.$emit('ok')
    }
  }
}
</script>
