<template>
  <r-div class="page" data-url="/oa/items" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th>{{ i18n['id'] }}</th>
              <th>{{ i18n['image'] }}</th>
              <th>{{ i18n['name'] }}</th>
              <th>{{ i18n['category'] }}</th>
              <th>{{ i18n['shop'] }}</th>
              <th>{{ i18n['brand'] }}</th>
              <th>{{ i18n['status'] }}</th>
              <th>{{ i18n['dri'] }}</th>
              <th>{{ i18n['hri'] }}</th>
              <th>{{ i18n['min_price'] }}</th>
              <th>{{ i18n['max_price'] }}</th>
              <th>{{ i18n['sold_qty'] }}</th>
              <th>
                <a href="#" @click.prevent="component = 'oa-items-new'; componentQuery = $data">新建</a>
              </th>
            </tr>
            <tr v-for="(item, i) in items">
              <td><router-link :to="{ name: 'oa-items-show', params: { id: item['id'] } }">{{ item['id'] }}</router-link></td>
              <td><img :data-src="item['image']" width="100" height="100"></td>
              <td>{{ item['name'] }}</td>
              <td>{{ item['categories'].map((category) => category['name']).join(' - ') }}</td>
              <td>{{ item['shop'] && item['shop']['name'] }}</td>
              <td>{{ item['brand']['name'] }}</td>
              <td>{{ i18n['statuses'][item['status']] }}</td>
              <td>{{ item['dri'] }}</td>
              <td>{{ item['hri'] }}</td>
              <td v-rmb="item['min_price']"></td>
              <td v-rmb="item['max_price']"></td>
              <td>{{ item['sold_qty'] }}</td>
              <td>
                <a href="#" @click.prevent="component = 'oa-items-edit'; componentQuery = { i18n: i18n, item: item, statuses: statuses }">编辑</a>
                <router-link :to="{ name: 'oa-items-skus-index', params: { itemId: item['id'] } }">SKU</router-link>
              </td>
            </tr>
          </table>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      items: [],
      statuses: [],
      categories: [],
      shops: [],
      brands: [],
      component: null,
      componentQuery: null
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.items = response.data['items']
      this.statuses = response.data['statuses']
      this.categories = response.data['categories']
      this.shops = response.data['shops']
      this.brands = response.data['brands']
    }
  }
}
</script>
