<template>
  <a href="#" @click.prevent="handleConfirm($event) && submit()" :data-loading="loading"><slot></slot></a>
</template>

<script>
import axios from '/concerns/axios'

export default {
  data() {
    return {
      loading: false
    }
  },
  methods: {
    handleConfirm(event) {
      if (this.$el.hasAttribute('data-confirm-destroy')) {
        return this.confirmDestroy(event, this.$el.dataset.confirmDestroy)
      } else if (this.$el.hasAttribute('data-confirm')) {
        return this.confirmWith(event, this.$el.dataset.confirm)
      } else {
        return true
      }
    },
    submit() {
      if (this.loading) {
        return
      }
      this.loading = this.$el.dataset.loadingName || true
      axios({
        method: this.$el.dataset.method || 'post',
        url: this.$el.getAttribute('href') || this.$el.dataset.url,
        data: this.$el.dataset.params
      }).then((response) => {
        this.$emit('ok', response)
      }).catch((error) => {
        this.handleError(error)
        this.$emit('fail', error)
      }).then(() => {
        this.loading = false
      })
    }
  }
}
</script>
