<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" method="patch" :action="'/oa/item/skus/' + q.sku['id']" @ok="go">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['price'] }}</div>
            <input type="number" v-model="price" class="border" v-placeholder min="0" step="0.01" v-autofocus required>
            <input type="hidden" name="price" :value="Math.round(price * 100)">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['qty'] }}</div>
            <input type="number" name="qty" :value="q.sku['qty']" class="border" v-placeholder required min="0">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['etd'] }}</div>
            <input type="number" name="etd" :value="q.sku['etd']" class="border" v-placeholder min="0">
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['image'] }}</div>
            <input type="file" name="image" class="border">
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="更新" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  data() {
    return {
      price: this.q.sku['price'] / 100
    }
  },
  methods: {
    go(response) {
      Object.assign(this.q.sku, response.data)
      this.$emit('close')
    }
  }
}
</script>
