    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['id'] }}</dt>
                <dd>{{ item['id'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['name'] }}</dt>
                <dd>{{ item['name'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['status'] }}</dt>
                <dd>{{ i18n['statuses'] && i18n['statuses'][item['status']] }}</dd>
