                <dd v-datetime="item['updated_at']"></dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['sku'] }}</dt>
                <dd>
                  <router-link :to="{ name: 'oa-items-skus-index', params: { itemId: item['id'] } }">查看</router-link>
