              <div class="li">
                <dt>{{ i18n['min_price'] }}</dt>
                <dd v-rmb="item['min_price']"></dd>
              </div>
              <div class="li">
                <dt>{{ i18n['max_price'] }}</dt>
                <dd v-rmb="item['max_price']"></dd>
              </div>
              <div class="li">
                <dt>{{ i18n['sold_qty'] }}</dt>
                <dd>{{ item['sold_qty'] }}</dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6 col-xl-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['shop'] }}</dt>
                <dd>{{ item['shop'] && item['shop']['name'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['brand'] }}</dt>
                <dd>{{ item['brand'] && item['brand']['name'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['category'] }}</dt>
                <dd>{{ item['categories'] && item['categories'].map((category) => category['name']).join(' - ') }}</dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6 col-xl-4">
