                <dt>{{ tagName(tagging['tag_id']) }}</dt>
                <dd>
                  <r-a :href="'/oa/item/taggings/' + tagging['id']" data-method="delete" @ok="item['taggings'].splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <div class="li mt">
          <main class="heading">{{ i18n['prop'] && i18n['prop']['model'] }}</main>
