        <div class="li mt">
          <main class="heading">{{ i18n['images'] }}</main>
        </div>
        <div class="cols list bg">
          <img v-for="img in item['images']" :src="img" class="block">
        </div>
        <div class="li mt">
          <main class="heading">{{ i18n['section'] && i18n['section']['model'] }}</main>
        </div>
        <div>
          <div class="flex bg px py" v-for="(section, i) in sections">
            <div class="mr fs0">
              <a href="#" @click.prevent="component = { is: 'oa-item-sections-edit', q: { data: $data, section: section } }">#{{ section['doi'] }}</a>
              <r-a :href="'/oa/item/sections/' + section['id']" data-method="delete" @ok="sections.splice(i, 1)" class="c-danger block mt" data-confirm-destroy>删除</r-a>
            </div>
            <div>
              <img :data-src="section['image_url']" class="block">
              <div class="my">{{ section['content'] }}</div>
            </div>
          </div>
          <div class="bg px py">
            <a href="#" @click.prevent="component = { is: 'oa-item-sections-new', q: { data: $data } }">添加</a>
          </div>
        </div>

        <component :is="component.is" :q="component.q" @close="component = {}"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      item: {},
      statuses: [],
      selects: [],
      options: [],
      tags: [],
      sections: [],
      component: {}
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.item = response.data['item']
      this.statuses = response.data['statuses']
      this.selects = response.data['selects']
      this.options = response.data['options']
      this.tags = response.data['tags']
      this.sections = response.data['sections']
    },
    tagName(id) {
      const tag = this.tags.find((tag) => tag['id'] === id)
      return tag ? tag['name'] : ''
    }
  }
}
</script>

<style scoped>
img {
  max-width: 100%;
}
</style>
