<template>
  <r-div class="page" :data-url="'/oa/items/' + $route.params.itemId + '/item/skus'" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="table-ctn yltd">
          <table>
            <tr>
              <th>{{ i18n['id'] }}</th>
              <th>{{ i18n['image'] }}</th>
              <th>{{ i18n['prop'] && i18n['prop']['model'] }}</th>
              <th>{{ i18n['price'] }}</th>
              <th>{{ i18n['qty'] }}</th>
              <th>{{ i18n['etd'] }}</th>
              <th>
                <a href="#" @click.prevent="component = 'oa-item-skus-new'; componentQuery = { data: $data, itemId: $route.params.itemId }">新建</a>
              </th>
            </tr>
            <tr v-for="(sku, i) in skus">
              <td>{{ sku['id'] }}</td>
              <td><img :data-src="sku['image']" width="100" height="100"></td>
              <td class="prel">{{ descProps(sku) }}</td>
              <td v-rmb="sku['price']"></td>
              <td>{{ sku['qty'] }}</td>
              <td>{{ sku['etd'] }}</td>
              <td>
                <a href="#" @click.prevent="component = 'oa-item-skus-edit'; componentQuery = { data: $data, sku: sku }">编辑</a>
                <r-a :href="'/oa/item/skus/' + sku.id" data-method="delete" @ok="skus.splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
              </td>
            </tr>
          </table>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      skus: [],
      props: [],
      component: null,
      componentQuery: null
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.skus = response.data['skus']
      this.props = response.data['props']
    },
    descProps(sku) {
      return this.props.filter((prop) => prop['sku_id'] === sku['id']).map((prop) => [prop['key'], prop['value']].join(': ')).join("\n")
    }
  }
}
</script>
