import Vue from 'vue'
import moment from 'moment'

Vue.directive('autofocus', {
  inserted(el) {
    if (!el.querySelector(':focus')) {
      el.focus()
    }
  }
})

Vue.directive('placeholder', {
  inserted(el, binding) {
    const label = binding.value ? document.querySelector(binding.value) : el.previousElementSibling
    label && (el.placeholder = '请输入' + label.innerText)
  }
})

Vue.directive('label', {
  componentUpdated(el, binding) {
    const input = binding.value ? document.querySelector(binding.value) : el.nextElementSibling
    input && (input.placeholder = '请输入' + el.innerText)
  }
})

Vue.directive('datetime', function (el, binding) {
  const m = moment(binding.value)
  let format
  switch (binding.arg) {
    case 'date':
      format = 'YYYY-MM-DD'
      break
    case 'd':
      format = 'MM-DD'
      break
    case 'time':
      format = 'hh:mm:ss'
      break
    case 't':
      format = 'mm:ss'
      break
    case 'datet':
      format = 'YYYY-MM-DD hh:mm'
      break
    case 'dtime':
      format = 'MM-DD hh:mm:ss'
      break
    case 'dt':
      format = 'MM-DD hh:mm'
      break
    default:
  }
  if (el.nodeName === 'INPUT') {
    format || (format = 'YYYY-MM-DDTHH:mm')
    el.value = m.isValid() ? m.format(format) : null
  } else {
    format || (format = 'YYYY-MM-DD HH:mm:ss')
    el.innerText = m.isValid() ? m.format(format) : null
  }
})

Vue.directive('rmb', function (el, binding) {
  const rmb = parseInt(binding.value) || 0
  let remainder = Math.abs(rmb)
  const quotient = Math.trunc(remainder / 100)
  remainder %= 100
  el.innerText = (rmb < 0 ? '-' : '') + '¥ ' + quotient + (remainder > 0 ? ('.' + Math.trunc(remainder / 10) + ((remainder %= 10) > 0 ? remainder : '')) : '')
})

Vue.directive('alert', {
  bind(el, binding) {
    el.addEventListener('click', function (event) {
      window.alert(binding.value)
    }, true)
  }
})

Vue.directive('confirm', {
  bind(el, binding) {
    el.addEventListener('click', function (event) {
      if (!window.confirm(binding.value)) {
        event.preventDefault()
        event.stopPropagation()
        event.stopImmediatePropagation()
      }
    }, true)
  }
})

Vue.directive('url', {
  bind(el, binding, vnode) {
    const sendAjax = () => {
      const method = el.dataset.method || 'post'
      return vnode.context.ajax(binding.value, {
        method: method,
        data: method !== 'get' ? [el.dataset.params, `${el.name}=${el.value}`].join('&') : null,
        params: method === 'get' ? [el.dataset.params, `${el.name}=${el.value}`].join('&') : null,
      }).then((response) => {
        if (vnode.data.on && vnode.data.on['ok']) {
          vnode.data.on['ok'].fns(response)
        }
        return response
      }).catch((error) => {
        if (vnode.data.on && vnode.data.on['fail']) {
          vnode.data.on['fail'].fns(error)
        }
        return Promise.reject(error)
      })
    }
    if (el.nodeName === 'INPUT' || el.nodeName === 'SELECT' || el.nodeName === 'TEXTAREA') {
      el.addEventListener('change', (event) => {
        window.requestAnimationFrame(() => {
          sendAjax().catch((error) => {
            if (['checkbox', 'radio'].includes(el.type)) {
              el.checked = !el.checked
            }
          })
        })
      }, false)
    } else {
      el.addEventListener('click', (event) => {
        window.requestAnimationFrame(sendAjax)
      }, false)
    }
  }
})

Vue.directive('route', {
  bind(el, binding, vnode) {
    el.dataset.cursor = 'pointer'
    if (el.nodeName === 'LABEL') {
      el.setAttribute('tabindex', 0)
    }
    const go = (event) => {
      if (el.dataset.routeless === 'true') {
        return
      }
      event.preventDefault()
      const router = vnode.context.$router, url = el.dataset.url
      if (url) {
        el.dataset.replace === 'true' ? router.replace(url) : router.push(url)
      }
    }
    el.addEventListener('click', go, false)
    el.addEventListener('keypress', (event) => {
      if (event.which === 13) {
        go(event)
      }
    }, false)
  }
})

Vue.directive('get', {
  inserted(el, binding, vnode) {
    vnode.context.ajax(binding.value)
  }
})
