<template lang="html">
  <a-action-page
    title="规格"
    :active="active"
    :toggler="toggler"
    @close="emitClose()"
    @open="requestItemSkus()">
    <div class="li mb">
      <img :data-src="current.sku ? current.sku['image_url'] : res.item['image_url']" width="80" height="80">
      <main>
        <div class="fs-xl bolder mb-md" v-rmb="current.sku ? current.sku['price'] : res.item['min_price']"></div>
        <div class="fs-sm mb-sm">{{ current.sku ? `已选: ${current.sku.name()}` : '请选择规格' }}</div>
        <div class="fs-xs">{{ current.sku && current.sku['qty'] < 50 ? `库存: ${current.sku.qty}` : '' }}</div>
      </main>
    </div>
    <div class="mb" v-for="(opts, key) in propKeysAndValues">
      <div class="li">
        <main class="heading">{{ key }}</main>
      </div>
      <div class="row gx-md gy-md tac">
        <label class="col-4 col-md-3 col-lg-2" v-for="(skus, value) in opts">
          <input ref="valueRadios" type="radio" :name="key" :value="value" :checked="current.sku && skus.includes(current.sku)" class="switch" @change.prevent="toggleSkus()">
          <div class="tag block border">{{ value }}</div>
        </label>
      </div>
    </div>
    <div class="w-sep"></div>
    <div class="li my-md">
      <main class="heading">数量</main>
      <number-input :min="current.sku && current.sku['qty'] > 0 ? 1 : 0" :max="current.sku ? current.sku.qty : 0" :value="current.qty" @ok="current.qty = $event; emitSku()"></number-input>
    </div>
    <div class="w-sep"></div>
    <div class="my">
      <div class="mb" v-if="current.sku && typeof current.sku['etd'] === 'number'">预计 {{ current.sku['etd'] }} 个工作日发货</div>
      <div class="fs-sm c-hint">* 部分地区物流受限，客服会在付款后跟您确认送货时间。</div>
    </div>
    <template v-slot:footer v-if="current.sku">
      <div class="cols bg hide-if-empty" v-if="current.sku['qty'] > 0">
        <template v-if="skuOnly"></template>
        <button type="button" class="btn primary" @click.prevent="emitCart()" v-else-if="cartOnly">
          确定
        </button>
        <template v-else>
          <button type="button" class="btn secondary" @click.prevent="emitCart()">
            加入购物车
          </button>
          <button type="button" class="btn primary" @click.prevent="emitBuy()">
            立即购买
          </button>
        </template>
      </div>
      <div class="btn w100 hint" v-else>
        当前商品已售罄
      </div>
    </template>
  </a-action-page>
</template>

<script>
export default {
  props: {
    active: { default: true },
    toggler: { default: null },
    itemId: { default: null },
    skuId: { default: null },
    skuQty: { default: null },
    skuOnly: { default: null },
    cartOnly: { default: null }
  },
  data() {
    return {
      res: {
        item: {},
        skus: [],
        sku: {
          props: []
        }
      },
      current: {
        sku: null,
        qty: 0,
      }
    }
  },
  watch: {
    itemId() {
      this.requestItemSkus()
    },
    skuId() {
      this.populateCurrentFromProps()
    },
    skuQty() {
      this.populateCurrentFromProps(true)
    }
  },
  computed: {
    propKeysAndValues() {
      const obj = {}
      this.res.sku.props.forEach((prop) => {
        if (!obj[prop['key']]) {
          obj[prop['key']] = {}
        }
        if (!obj[prop['key']][prop['value']]) {
          obj[prop['key']][prop['value']] = []
        }
        obj[prop['key']][prop['value']].push(prop.sku)
      })
      return obj
    }
  },
  methods: {
    requestItemSkus() {
      if (!this.itemId || this.itemId === this.res.item['id']) {
        return
      }
      this.ajax(`/init/item_skus?item_id=${this.itemId}`, {}, (response) => {
        response.data.skus.forEach((sku) => {
          sku.props = []
          response.data.sku.props.forEach((prop) => {
            if (prop['sku_id'] === sku['id']) {
              sku.props.push(prop)
              prop.sku = sku
            }
          })
          sku.name = function (delimiter) {
            return this.props.map((prop) => prop['value']).join(delimiter || ' ')
          }
        })
        Object.assign(this.res, response.data)
      }).then((response) => {
        this.populateCurrentFromProps()
      })
    },
    populateCurrentFromProps(skuUnchanged) {
      if (!skuUnchanged) {
        this.current.sku = this.res.skus.find((sku) => sku['id'] == this.skuId)
      }
      this.current.qty = this.skuQty ? parseInt(this.skuQty) : (
        this.current.sku && this.current.sku['qty'] > 0 ? 1 : 0
      )
      this.emitSku()
    },
    toggleSkus() {
      const checkedRadios = this.$refs.valueRadios.filter((input) => input.checked)
      if (checkedRadios.length === Object.keys(this.propKeysAndValues).length) {
        const checkedSkus = checkedRadios.map((input) => this.propKeysAndValues[input.name][input.value]).reduce((resultSkus, skus) => {
          if (resultSkus === null) {
            return skus
          } else {
            return resultSkus.filter((sku) => skus.includes(sku))
          }
        }, null) || []
        if (checkedSkus.length === 1) {
          this.current.sku = checkedSkus[0]
          this.current.qty = this.current.sku['qty'] > 0 ? (
            (this.current.sku.qty || Infinity) < this.current.qty ? this.current.sku.qty : (
              this.current.qty > 0 ? this.current.qty : 1
            )
          ) : 0
          this.emitSku()
        }
      }
    },
    emitClose() {
      this.$emit('close')
    },
    emitSku() {
      if (this.current.qty > 0) {
        this.$emit('sku', this.current)
      }
    },
    emitCart() {
      if (this.current.qty > 0) {
        this.emitClose()
        this.$emit('cart', this.current)
      }
    },
    emitBuy() {
      if (this.current.qty > 0) {
        this.emitClose()
        this.$emit('buy', this.current)
      }
    }
  }
}
</script>
