<template>
  <r-div class="page bg" :data-url="$route.fullPath" @ok="come">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">商品</div>
      <div class="subheader cols w-tabs fs-sm">
        <label>
          <input type="radio" name="order_by" value="recommendation" v-model="filterOpts['order_by']" @change="reload">
          <div class="centery">推荐</div>
        </label>
        <label>
          <input type="radio" name="order_by" value="new_arrival" v-model="filterOpts['order_by']" @change="reload">
          <div class="centery">新品</div>
        </label>
        <label>
          <input v-if="filterOpts['order_by'] !== 'price'" type="radio" name="order_by" value="price" v-model="filterOpts['order_by']" @change="reload">
          <input v-else type="checkbox" name="order_desc" v-model="filterOpts['order_desc']" @change="reload">
          <div class="centery">价格<span></span></div>
        </label>
        <button ref="filterToggler" type="button" @click="toggle.filter = true">筛选</button>
      </div>
    </header>
    <main class="ctn">
      <div class="row gx-sm gy-lg list">
        <router-link :to="{ name: 'mbr-items-show', params: { id: item['id'] } }" class="col-6 col-md-4 col-lg-3 col-xl-2 inh-fg" v-for="item in res.items">
          <img :data-src="item['image_url']" class="block" width="1" height="1">
          <div class="pr-em">
            <div class="mt-md fs-xs c-info">{{ item['brand_name'] }}</div>
            <div class="my-sm bolder">{{ item['name'] }}</div>
            <div class="mb-xs fs-xl bolder" v-rmb="item['min_price']"></div>
            <div class="fs-xs" v-if="res.tags[item['id']]">
              <div class="tag major mx-xs my-xs" v-for="tagName in res.tags[item['id']]">{{ tagName }}</div>
            </div>
          </div>
        </router-link>
      </div>

      <a-action-page
        title="筛选"
        :active="toggle.filter"
        :toggler="$refs.filterToggler"
        @close="toggle.filter = false">
        <form @submit.prevent="submitFilterForm">
          <div>
            <div class="li">
              <main class="heading">价格区间</main>
            </div>
            <div class="row gx-md gy-md tac">
              <label class="col-6 col-md-4 col-lg-3 col-xl-2">
                <input type="number" name="min_price" max="1000000000" min="0" placeholder="请输入最低价" class="filter-input" v-model="filterOpts['min_price']">
              </label>
              <label class="col-6 col-md-4 col-lg-3 col-xl-2">
                <input type="number" name="max_price" max="1000000000" :min="filterOpts['min_price'] || 0" placeholder="请输入最高价" class="filter-input" v-model="filterOpts['max_price']">
              </label>
            </div>
          </div>
          <div class="my" v-for="(filterOptions, filterName) in res.filters">
            <div class="li">
              <main class="heading">{{ filterName }}</main>
            </div>
            <div class="row gx-md gy-md tac">
              <label class="col-4 col-md-3 col-lg-2" v-for="filterOption in filterOptions">
                <input type="checkbox" :name="filterName + '[]'" :value="filterOption" class="switch" v-model="filterOpts[filterName + '[]']">
                <div>{{ filterOption || '无' }}</div>
              </label>
            </div>
          </div>
          <div>
            <input type="submit" id="filter-form-submit" class="hidden">
          </div>
        </form>
        <template v-slot:footer>
          <div class="cols h100">
            <input type="reset" value="重置" class="btn secondary" @click.prevent="resetFilterForm">
            <label for="filter-form-submit" class="btn primary">确定<label>
          </div>
        </template>
        <div class="br-footer expd"></div>
      </a-action-page>
    </main>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        items: null,
        tags: null,
        filters: null
      },
      filterOpts: {
        'order_by': this.$route.query['order_by'] || 'recommendation',
        'order_desc': [true, 'true'].includes(this.$route.query['order_desc']),
        'min_price': this.$route.query['min_price'],
        'max_price': this.$route.query['max_price'],
        '场景[]': [].concat(this.$route.query['场景[]']),
        '风格[]': [].concat(this.$route.query['风格[]'])
      },
      toggle: {
        filter: false
      }
    }
  },
  methods: {
    come(response) {
      Object.assign(this.res, response.data)
    },
    reload() {
      const query = Object.assign({}, this.$route.query)
      Object.entries(this.filterOpts).forEach(([key, value]) => {
        delete query[key]
        value && (query[key] = value)
      });
      this.$router.replace({ name: 'mbr-items-index', query: query })
    },
    submitFilterForm() {
      this.toggle.filter = false
      this.reload()
    },
    resetFilterForm() {
      this.filterOpts['min_price'] = null
      this.filterOpts['max_price'] = null
      this.filterOpts['场景[]'] = []
      this.filterOpts['风格[]'] = []
    }
  }
}
</script>

<style lang="scss" scoped>
.filter-input {
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 14px;
  background: var(--body-bg);
  border: none;
}
</style>
