import Vue from 'vue'

import axios from '/concerns/axios'
import Session from '/concerns/session'

import defaultAvatarImage from '/images/common/default1-1.svg'

Vue.mixin({
  methods: {
    log(...msgs) {
      console.log(...msgs);
    },
    stopEverything(event) {
      event.preventDefault()
      event.stopPropagation()
      event.stopImmediatePropagation()
      return false
    },
    confirmWith(event, msg) {
      return window.confirm(msg) || this.stopEverything(event)
    },
    confirmDestroy(event, msg) {
      return this.confirmWith(event, msg || '确定删除吗？')
    },
    pushState(url) {
      return history.pushState({}, '', url)
    },
    replaceState(url) {
      return history.replaceState({}, '', url)
    },
    ajax(url, opts = {}, responseFn) {
      this.$el.dataset.loading = this.$el.dataset.loadingName || 'fullscreen'
      return axios(Object.assign({
        method: 'get',
        url: url
      }, opts)).then((response) => {
        if (responseFn) {
          responseFn(response)
        } else if (this.hasOwnProperty('res')) {
          Object.assign(this.res, response.data)
        }
        return response
      }).catch((error) => {
        this.handleError(error)
        return Promise.reject(error)
      }).finally(() => {
        this.$el.dataset.loading = false
      })
    },
    handleError(error) {
      if (error.response) {
        switch (error.response.status) {
          case 401:
            localStorage.removeItem('feauth')
            this.$router.replace({
              path: '/signin',
              query: {
                redirection: this.$route.fullPath
              }
            })
            break
          default: // 422 and others
            if (/json/.test(error.response.headers['content-type'])) {
              window.alert(error.response.data)
            } else {
              this.$root.$el.innerHTML = error.response.data
            }
        }
      } else if (error.request) {
        window.alert('网络/服务故障，请稍后重试')
      } else {
        window.alert(error.message)
      }
    },
    userAvatar(url) {
      return url || defaultAvatarImage
    },
    signedIn() {
      return Session.signedIn()
    }
  }
});
