<template>
  <r-div class="page" :data-url="'/oa/item/categories/' + $route.params.id" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-6">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['id'] }}</dt>
                <dd>{{ category['id'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['name'] }}</dt>
                <dd>{{ category['name'] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['level'] }}</dt>
                <dd>{{ i18n['levels'] && i18n['levels'][category['level']] }}</dd>
              </div>
              <div class="li">
                <dt>{{ i18n['created_at'] }}</dt>
                <dd v-datetime="category['created_at']"></dd>
              </div>
              <div class="li">
                <dt>{{ i18n['updated_at'] }}</dt>
                <dd v-datetime="category['updated_at']"></dd>
              </div>
            </dl>
          </div>
          <div class="col-md-6">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['selection'] }}</dt>
                <dd>
                  <a href="#" @click.prevent="component = 'oa-item-category-selections-new'; componentQuery = { data: $data, category: category }">添加</a>
                </dd>
              </div>
              <div class="li" v-for="(selection, i) in category['selections']" v-if="category['selections']">
                <dt>{{ selection['select']['name'] }}</dt>
                <dd>
                  <r-a :href="'/oa/item/category/selections/' + selection['id']" data-method="delete" @ok="category['selections'].splice(i, 1)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      category: {},
      levels: [],
      selects: [],
      component: null,
      componentQuery: null
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data['i18n']
      this.category = response.data['category']
      this.levels = response.data['levels']
      this.selects = response.data['selects']
    }
  }
}
</script>
