<template lang="html">
  <div class="li">
    <main>
      <input ref="code" type="tel" name="code" placeholder="请输入验证码" class="input" maxlength="6" required>
    </main>
    <div class="w40 tac">
      <r-a class="input inh-fg" :href="url" :data-params="mobile ? `mobile=${mobile}` : false" @ok="countDown($event.data['id'])" @fail="$emit('fail')" v-if="countdown === 0">发送验证码</r-a>
      <div class="input" v-else>{{ countdown }}s</div>
    </div>
    <input ref="captcha" type="hidden" name="captcha_id" required>
  </div>
</template>

<script>
export default {
  props: ['url', 'mobile'],
  data() {
    return {
      countdown: 0
    }
  },
  methods: {
    countDown(id) {
      this.$refs.code.focus()
      this.$refs.code.value = ''
      this.$refs.captcha.value = id
      this.countdown = 30
      const countdownInterval = window.setInterval(() => {
        this.countdown -= 1
        if (this.countdown === 0) {
          window.clearInterval(countdownInterval)
        }
      }, 1000)
    }
  }
}
</script>
