<template>
  <r-div class="page" data-url="/oa/item/categories" @ok="come">
    <oa-header></oa-header>
    <div class="ctn nav-ctn">
      <oa-nav></oa-nav>
      <main>
        <div class="row gx gy">
          <div class="col-md-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ i18n['levels'] && i18n['levels']['l1'] }}</dt>
                <dd><a href="#" @click.prevent="openNew()">添加</a></dd>
              </div>
              <div class="li" v-for="(category, i) in l1Categories()">
                <img :data-src="category['image']" width="50" height="50" v-if="category['image']" class="block">
                <dt>
                  <router-link :to="{ name: 'oa-item-categories-show', params: { id: category.id } }">#{{ category['id'] }} {{ category['name'] }}</router-link>
                  <div>{{ i18n['dri'] }} {{ category['dri'] }}</div>
                  <div>{{ i18n['item_'] }} {{ counts[category['id']] || 0 }}</div>
                </dt>
                <dd>
                  <a href="#" @click.prevent="openEdit(category)">编辑</a>
                  <r-a :href="'/oa/item/categories/' + category.id" data-method="delete" @ok="afterDestroy(category)" class="c-danger" data-confirm-destroy>删除</r-a>
                  <a class="block mt" href="#" @click.prevent="selected.l1Category = category; selected.l2Category = null">展开下级</a>
                </dd>
              </div>
            </dl>
          </div>
          <div class="col-md-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ selected.l1Category && selected.l1Category.name || i18n['levels'] && i18n['levels']['l2'] }}</dt>
                <dd><a href="#" @click.prevent="openNew(selected.l1Category)" v-if="selected.l1Category">添加</a></dd>
              </div>
              <div class="li" v-for="(category, i) in l2Categories()">
                <img :data-src="category['image']" width="50" height="50" v-if="category['image']">
                <dt>
                  <router-link :to="{ name: 'oa-item-categories-show', params: { id: category.id } }">#{{ category['id'] }} {{ category['name'] }}</router-link>
                  <div>{{ i18n['dri'] }} {{ category['dri'] }}</div>
                  <div>{{ i18n['item_'] }} {{ counts[category['id']] || 0 }}</div>
                </dt>
                <dd>
                  <a href="#" @click.prevent="openEdit(category)">编辑</a>
                  <r-a :href="'/oa/item/categories/' + category.id" data-method="delete" @ok="afterDestroy(category)" class="c-danger" data-confirm-destroy>删除</r-a>
                  <a class="block mt" href="#" @click.prevent="selected.l2Category = category">展开下级</a>
                </dd>
              </div>
            </dl>
          </div>
          <div class="col-md-4">
            <dl class="px bg">
              <div class="li">
                <dt>{{ selected.l2Category && selected.l2Category.name || i18n['levels'] && i18n['levels']['l3'] }}</dt>
                <dd><a href="#" @click.prevent="openNew(selected.l2Category)" v-if="selected.l2Category">添加</a></dd>
              </div>
              <div class="li" v-for="(category, i) in l3Categories()">
                <img :data-src="category['image']" width="50" height="50" v-if="category['image']">
                <dt>
                  <router-link :to="{ name: 'oa-item-categories-show', params: { id: category.id } }">#{{ category['id'] }} {{ category['name'] }}</router-link>
                  <div>{{ i18n['dri'] }} {{ category['dri'] }}</div>
                  <div>{{ i18n['item_'] }} {{ counts[category['id']] || 0 }}</div>
                </dt>
                <dd>
                  <a href="#" @click.prevent="openEdit(category)">编辑</a>
                  <r-a :href="'/oa/item/categories/' + category.id" data-method="delete" @ok="afterDestroy(category)" class="c-danger" data-confirm-destroy>删除</r-a>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <component :is="component" :q="componentQuery" @close="component = null"></component>
      </main>
    </div>
  </r-div>
</template>

<script>
export default {
  data() {
    return {
      i18n: {},
      categories: [],
      counts: {},
      component: null,
      componentQuery: null,
      selected: {
        l1Category: null,
        l2Category: null
      }
    }
  },
  methods: {
    come(response) {
      this.i18n = response.data.i18n
      this.categories = response.data.categories
      this.counts = response.data.counts
    },
    l1Categories() {
      return this.categories.filter((category) => category['level'] === 'l1')
    },
    l2Categories() {
      return this.selected.l1Category ? this.categories.filter((category) => category['level'] === 'l2' && category['category_id'] === this.selected.l1Category.id) : []
    },
    l3Categories() {
      return this.selected.l2Category ? this.categories.filter((category) => category['level'] === 'l3' && category['category_id'] === this.selected.l2Category.id) : []
    },
    openNew(category) {
      this.component = 'oa-item-categories-new'
      this.componentQuery = {
        data: this.$data,
        category: category
      }
    },
    openEdit(category) {
      this.component = 'oa-item-categories-edit'
      this.componentQuery = {
        data: this.$data,
        category: category
      }
    },
    afterDestroy(destroyedCategory) {
      this.categories.splice(this.categories.findIndex((category) => category.id === destroyedCategory.id), 1)
    }
  }
}
</script>
