<template lang="html">
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">订单详情</div>
    </header>
    <footer class="ctn">
      <div class="cols">
        <button type="button" class="btn body-bg c-hint" v-if="nextStatus('canceled')">
          取消订单
        </button>
        <button type="button" class="btn primary" v-if="nextStatus('paid')">
          立即付款
        </button>
      </div>
    </footer>
    <main class="ctn">
      <div class="tac pt-md pb-lg">
        <div class="fs-xl bolder larger">{{ res.i18n.statuses[res.order['status']] }}</div>
      </div>
      <div class="w-sep expd"></div>
      <div>
        <div class="li my">
          <div class="shift w20">{{ res.i18n.address['name'] }}</div>
          <div class="c-info">{{ res.order['address_name'] }} {{ res.order['address_mobile'] }}</div>
        </div>
        <div class="li my">
          <div class="shift w20">{{ res.i18n.address_ }}</div>
          <div class="c-info">{{ res.order['address_province'] }} {{ res.order['address_city'] }} {{ res.order['address_district'] }} {{ res.order['address_street'] }}</div>
        </div>
      </div>
      <div class="w-sep expd"></div>
      <div class="w-seps">
        <router-link class="li py" :to="{ name: 'mbr-items-show', params: { id: skuship['item_id'] } }" v-for="skuship in groupedSkuships">
          <img class="fs0" :data-src="skuship['image_url']" width="80" height="80">
          <div class="fg1">
            <div class="fs-sm bolder">
              {{ skuship['brand_name'] }} · {{ skuship['item_name'] }}
            </div>
            <div class="fs-xs mt-sm">
              {{ skuship['propships'].map((propship) => propship['value']).join(' ') }}
            </div>
            <div class="fs-xs mt-sm" v-if="skuship['etd']">
              预计 {{ skuship['etd'] }} 个工作日发货
            </div>
            <div class="fs-sm mt-sm">
              x {{ skuship['qty'] }}
            </div>
          </div>
          <div class="fs0 fs-sm bolder" v-rmb="skuship['price']"></div>
        </router-link>
      </div>
      <div class="w-sep expd"></div>
      <div class="my">
        <div class="li py-sm my-md">
          <main class="c-hint">{{ res.i18n.order['price'] }}</main>
          <div v-rmb="res.order['price']"></div>
        </div>
        <div class="li py-sm my-md">
          <main class="c-hint">{{ res.i18n.order['shipping_price'] }}</main>
          <div v-rmb="res.order['shipping_price']"></div>
        </div>
        <div class="li py-sm my-md">
          <main class="c-hint">{{ res.i18n.order['cost'] }}</main>
          <div v-rmb="res.order['cost']"></div>
        </div>
      </div>
      <dl class="w-seps w-seps-tb my">
        <div class="li">
          <dt class="shift fs0">{{ res.i18n.order['remark'] }}</dt>
          <dd class="prel">{{ res.order['remark'] }}</dd>
        </div>
        <div class="li">
          <dt class="shift">{{ res.i18n.order['id'] }}</dt>
          <dd>{{ res.order['id'] }}</dd>
        </div>
        <div class="li">
          <dt class="shift">{{ res.i18n.order['created_at'] }}</dt>
          <dd v-datetime="res.order['created_at']"></dd>
        </div>
        <div class="li" v-if="res.order['paid_at']">
          <dt class="shift">{{ res.i18n.order['paid_at'] }}</dt>
          <dd v-datetime="res.order['paid_at']"></dd>
        </div>
        <div class="li" v-if="res.order['completed_at']">
          <dt class="shift">{{ res.i18n.order['completed_at'] }}</dt>
          <dd v-datetime="res.order['completed_at']"></dd>
        </div>
        <div class="li" v-if="res.order['refunded_at']">
          <dt class="shift">{{ res.i18n.order['refunded_at'] }}</dt>
          <dd v-datetime="res.order['refunded_at']"></dd>
        </div>
        <div class="li" v-if="res.order['expired_at']">
          <dt class="shift">{{ res.i18n.order['expired_at'] }}</dt>
          <dd v-datetime="res.order['expired_at']"></dd>
        </div>
        <div class="li" v-if="res.order['canceled_at']">
          <dt class="shift">{{ res.i18n.order['canceled_at'] }}</dt>
          <dd v-datetime="res.order['canceled_at']"></dd>
        </div>
      </dl>
      <div class="br-footer expd"></div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        i18n: {
          order: {},
          address: {},
          statuses: {}
        },
        order: {
          skuships: []
        }
      }
    }
  },
  mounted() {
    this.ajax(`/mbr/item/orders/${this.$route.params['id']}`)
  },
  computed: {
    groupedSkuships() {
      return this.res.order.skuships.reduce((result, skuship) => {
        if (result.has(skuship['group_code'])) {
          result.get(skuship['group_code'])['qty'] += 1
        } else {
          result.set(skuship['group_code'], skuship)
          result.get(skuship['group_code'])['qty'] = 1
        }
        return result
      }, new Map()).values()
    },
    nextStatuses() {
      return this.res.order && this.res.order['next_statuses'] || []
    }
  },
  methods: {
    nextStatus(status) {
      return this.nextStatuses.includes(status)
    }
  }
}
</script>

<style lang="css" scoped>
