<template>
  <component :is="`${portal}-signin`" @ok="redirect()"></component>
</template>

<script>
export default {
  mounted() {
    this.ajax('/sessions/new').then((response) => {
      if (this.signedIn()) {
        this.redirect()
      }
    })
  },
  computed: {
    portal() {
      return this.$route.query.redirection && this.$route.query.redirection.startsWith('/oa') ? 'oa' : 'mbr'
    },
    redirection() {
      return this.$route.query.redirection || `/${portal}`
    }
  },
  methods: {
    redirect() {
      this.$router.replace(this.redirection)
    }
  }
}
</script>
