<template>
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
      <div class="c">我的订单</div>
      <div class="subheader cols w-tabs fs-sm">
        <label v-for="status in ['', 'unpaid', 'paid', 'completed']">
          <input type="radio" name="status" :value="status" v-model="currentStatus">
          <div class="centery">{{ res.i18n.statuses[status] || '全部' }}</div>
        </label>
      </div>
    </header>
    <main class="ctn">
      <div class="expd"></div>
      <div class="list w-seps expd">
        <router-link class="block pdx py inh-fg" :to="{ name: 'mbr-item-orders-show', params: { id: order['id'] } }" v-for="order in res.orders">
          <div class="li fs-sm mt-sm">
            <main class="c-hint">{{ res.i18n.order['id'] }}: {{ order['id'] }}</main>
            <div class="bolder">{{ res.i18n.statuses[order['status']] }}</div>
          </div>
          <div class="w-seps">
            <div class="li py-lg" v-for="skuship in groupedSkuships(order['skuships'])">
              <img class="fs0" :data-src="skuship['image_url']" width="80" height="80">
              <div class="fg1">
                <div class="fs-sm mr-xl">
                  {{ skuship['brand_name'] }} · {{ skuship['item_name'] }}
                </div>
                <div class="fs-xs c-info mt-sm">
                  {{ skuship['propships'].map((propship) => propship['value']).join(' ') }}
                </div>
                <div class="fs-sm mt-sm">
                  x {{ skuship['qty'] }}
                </div>
              </div>
              <div class="fs0" v-rmb="skuship['price']"></div>
            </div>
          </div>
          <div class="mb-sm tar bolder">
            共 {{ order['skuships'].length }} 件，{{ res.i18n.order['price'] }} <span v-rmb="order['price']"></span>
          </div>
        </router-link>
      </div>
      <div class="br-footer expd"></div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      res: {
        i18n: {
          order: {},
          statuses: {}
        },
        orders: []
      },
      currentStatus: this.$route.query['status'] || ''
    }
  },
  mounted() {
    this.getOrders(this.currentStatus)
  },
  watch: {
    currentStatus() {
      this.getOrders(this.currentStatus)
    }
  },
  methods: {
    getOrders(status) {
      const params = {
        status: status
      }
      this.ajax('/mbr/item/orders', { params: params }).then((response) => {
        this.replaceState(`${window.location.pathname}?${new URLSearchParams(params).toString()}`)
      })
    },
    groupedSkuships(skuships) {
      return skuships.reduce((result, skuship) => {
        if (result.has(skuship['group_code'])) {
          result.get(skuship['group_code'])['qty'] += 1
        } else {
          result.set(skuship['group_code'], skuship)
          result.get(skuship['group_code'])['qty'] = 1
        }
        return result
      }, new Map()).values()
    }
  }
}
</script>
