<template>
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
    </header>
    <main class="ctn">
      <div class="fs-xl bolder mt-lg mb-md">{{ signinTitle }}</div>
      <div class="fs-sm c-hint mb-xl">{{ signinHint }}</div>
      <r-form class="row" action="/sessions" @ok="$emit('ok')">
        <input type="hidden" name="category" :value="category">
        <div class="col-md-6">
          <input ref="mobile" type="tel" name="mobile" v-model="mobile" placeholder="请输入手机号" class="input" maxlength="11" required v-autofocus>
          <mobile-captcha v-if="byMobileCaptcha || byMobileCaptchaPassword" :url="`/captchas?type=${captchaType}`" :mobile="mobile" @fail="$refs.mobile.focus()"></mobile-captcha>
          <input v-if="byMobile" type="password" name="password" placeholder="请输入密码" class="input" required>
          <input v-if="byMobileCaptchaPassword" type="password" name="password" placeholder="新密码 (至少8位，包含字母和数字)" class="input" required>
          <div class="br-md"></div>
          <input type="submit" value="登录" class="btn primary block my-lg">
        </div>
      </r-form>
      <div class="li">
        <main>
          <button @click="switchCategory()" class="fs-sm">{{ switchHint }}</button>
        </main>
        <button @click="switchCategory('by_mobile_captcha_password')" class="fs-sm" v-if="byMobile">忘记密码？</button>
      </div>
      <div class="br-xl my-xl"></div>
      <p class="tac fs-sm c-hint">
        <span>登录即表示您已阅读并同意百安谊选</span>
        <br>
        <router-link :to="{ name: 'terms_of_use', query: { backable: true } }" class="c-major">《用户协议》</router-link>
        <span>和</span>
        <router-link :to="{ name: 'privacy_policy', query: { backable: true } }" class="c-major">《隐私政策》</router-link>
      </p>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobile: null,
      category: this.$route.query['category'] || 'by_mobile_captcha'
    }
  },
  computed: {
    byMobile() {
      return this.category === 'by_mobile'
    },
    byMobileCaptcha() {
      return this.category === 'by_mobile_captcha'
    },
    byMobileCaptchaPassword() {
      return this.category === 'by_mobile_captcha_password'
    },
    signinTitle() {
      return this.byMobileCaptchaPassword ? '忘记密码？' : '登录'
    },
    signinHint() {
      return this.byMobileCaptchaPassword ? '验证手机号并设置新密码' : this.byMobile ? '使用密码登录' : '使用短信验证码登录'
    },
    switchHint() {
      return this.byMobile ? '使用短信验证码登录' : '使用密码登录'
    },
    captchaType() {
      return this.byMobileCaptchaPassword ? 'password_forgetting_mobile' : 'signin_mobile'
    }
  },
  methods: {
    switchCategory(newCategory) {
      this.category = newCategory || (this.byMobile ? 'by_mobile_captcha' : 'by_mobile')
      this.$refs.mobile.focus()
    }
  }
}
</script>
