<template>
  <a-dialog @close="$emit('close')">
    <div>
      <main>
        <r-form class="form row gx" method="patch" :action="'/oa/item/categories/' + q.category['id']" @ok="go">
          <input type="hidden" name="category_id" :value="q.category['id']" v-if="q.category">
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['name'] }}</div>
            <input type="text" name="name" :value="q.category['name']" class="border" v-placeholder v-autofocus>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['dri'] }}</div>
            <input type="text" name="dri" :value="q.category['dri']" class="border" v-placeholder>
          </label>
          <label class="col-md-6 col-lg-4 col-xl-3">
            <div>{{ q.data.i18n['image'] }}</div>
            <input type="file" name="image" class="border" v-placeholder>
          </label>
          <div class="col-md-6 col-lg-4 col-xl-3">
            <input type="submit" value="更新" class="btn primary block">
          </div>
        </r-form>
      </main>
    </div>
  </a-dialog>
</template>

<script>
export default {
  props: ['q'],
  methods: {
    go(response) {
      Object.assign(this.q.category, response.data)
      this.$emit('close')
    }
  }
}
</script>
